import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { DetailsTransactionsPortfolioComponent } from 'src/app/PopUp/details-transactions-portfolio/details-transactions-portfolio.component';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { ManageDataService } from 'src/services/manage-data.service';
import { PortafolioService } from 'src/services/portafolio/portafolio.service';
import { SocketioService } from 'src/services/WebSocket/socketio.service';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

@Component({
  selector: 'app-table-info-transactions',
  templateUrl: './table-info-transactions.component.html',
  styleUrls: ['./table-info-transactions.component.scss']
})
export class TableInfoTransactionsComponent implements OnInit, OnDestroy, AfterViewInit {
  hiddenitem:boolean=false;
  dataSource: MatTableDataSource<any>;
  displayedColumns = ["transaction_type", "creation_time", "base_currency", "qty", "price", "total", "transaction_id"];
  portfolio_name: string = "";
  portfolioTransactions = [];
  transactions = [];
  table_data = [];
  subscription: Subscription = new Subscription();
  collectionSize: number;
  sort_value: any = 1;
  show:boolean=false;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @Input() typetable: string;

  constructor(private toastr: ToastrService, private assets: PortafolioService,
    private manage_data: ManageDataService<any>,
    private dialog: MatDialog, private socket:SocketioService,
    public breakpointObserver: BreakpointObserver) {
      
    this.mediaQuery();
  }

  ngAfterViewInit() {
  }

  async ngOnInit(): Promise<void> {
    
    await this.GetTransactions();
    await this.filterbybasecurrency(this.typetable);
    this.GetDataBloxOrder();   

  }

  async GetDataBloxOrder() {
    this.subscription.add(this.socket.GetDataBloxOrder().subscribe((data: any) => {
      if (typeof data != 'object' && data != "Hey Dude!") {
        if (JSON.parse(data).success == null || JSON.parse(data).success == undefined) {
          this.ResponseEvent(JSON.parse(data))
        }
      }
    }))
  }

  ResponseEvent(msg: any) {
    switch (msg.header.msg_type) {
      case 'EXECUTION':
      case 'TRACKING_EXECUTION':
      case 'DEPOSIT':
      case 'WITHDRAW':
      case 'ORDER_ERROR':
      case 'ORDER_CANCEL':
      case 'ORDER_REJECTED':
      case 'TRACKING_EXECUTION':
        // console.log("Llego notificacion Socket");
        setTimeout(async () => {
          await this.GetTransactions();
          await this.filterbybasecurrency(this.typetable);
        }, 500);
        break;
      default:
        break;
    }
  }

  async filterbybasecurrency(data:string){
    this.show = true;
    if(data !== 'home'){
      let product = await this.dataSource.data.filter(x => `${x.base_currency}` === data);
      product = product.sort((a, b) => {
        if (a.creation_time > b.creation_time) return -1;
        else return 1;
      })
      this.dataSource.data = product
      if(JSON.stringify(product) == "[]"){
        this.show = false;
      }
    }

  }

  ngOnDestroy(): void {

    this.subscription.unsubscribe();
  }

  ApplyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  ShowDetails(data: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = false;
    dialogConfig.width = "40rem";
    this.manage_data.emitEvent(EnumEventsPopUp.DetailsPortfolio.toString(), data);
    this.dialog.open(DetailsTransactionsPortfolioComponent, dialogConfig);
  }

  GetColor(value: any) {
    if (Number(value) < 0) {
      return 'red';
    } else {
      return 'green'
    }
  }

  ShowError(msg: string, title: string = "", disableTimeOut: boolean = true) {
    // console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,
      positionClass: 'toast-center-center',
    });
  }

  async GetTransactions() {
    //===========================================================================
    //Get Default portafolio
    //===========================================================================
    let getDefaultPortafolioResponse = await this.GetDefaultPortafolio();
    if (!getDefaultPortafolioResponse) {
      this.ShowError("Error to get client portafolio");
      return;
    }

    this.portfolio_name = getDefaultPortafolioResponse.name;

    //===========================================================================
    //Get Portafolio Transactions
    //===========================================================================
    const PortfolioName: any = {
      portfolio_name: this.portfolio_name
    }

    let getPortafolioTransactionoResponse: any = await this.GetPortafolioTransaction(PortfolioName);
    if (!getPortafolioTransactionoResponse) {
      this.ShowError("Error to get portfolio transactions");
      return;
    }
    this.transactions = [];
    this.transactions = getPortafolioTransactionoResponse.sort((a, b) => {
      if (a.creation_time > b.creation_time) return -1;
      else return 1;
    })
    // console.log("Transactions ",this.transactions)

    this.table_data = [];
    this.transactions.filter(element => {

      element.total = Number((element.qty * element.price) + element.fee_amount);
      // console.log("Elements Transactions",element)
      if (isNaN(element.description) || element.transaction_type == "WITHDRAW") {
        this.table_data.push(element);
      } else {
        var value = this.transactions.find(x => x.transaction_id == Number(element.description) && x.base_currency != 'USD')
        if (value) {
          value.qty = value.qty * -1;
          value.total = element.total;
          this.table_data.push(value);
        }
      }
    })

    this.table_data = this.table_data.filter(element => {
      if (element.transaction_type != 'HOLD') {
        element.qty = element.qty < 0 ? element.qty * -1 : element.qty
        return element;
      }
    })
    // console.log("Tabla after", this.table_data);
    this.dataSource = new MatTableDataSource(this.table_data)
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.dataSource.data = this.table_data; 
  }

  // Sort() {
  //   this.sort_value = this.sort_value * -1;
  //   this.transactions = this.transactions_data.sort((a, b) => {
  //     if (a.creation_time > b.creation_time) return this.sort_value * -1;
  //     else return this.sort_value;
  //   })
  // }

  GetDefaultPortafolio(): Promise<PortafolioDefault> {
    return new Promise<PortafolioDefault>((resolve, reject) => {
      this.subscription.add(this.assets.GetDefault().subscribe(response => {
        resolve(response);
      }, err => {
        console.log("Error GetDefault:", err);
        resolve(null);
      }));
    });
  }

  GetPortafolioTransaction(data: any): Promise<any> {

    return new Promise((resolve, reject) => {
      this.subscription.add(this.assets.GetPortafolioTransaction(data).subscribe(response => {
        resolve(response);
      }, err => {
        console.log("Error GetPortfolioTransactions:", err);
        resolve(null);
      }));
    });
  }

public mediaQuery() {
    this.subscription.add(this.breakpointObserver
    .observe(Breakpoints.XSmall)
    .subscribe((state: BreakpointState) => {
      if (state.matches) {
      //AQUI SERA TRUE SOLO SI ESTA EN RESOLUCION DE CELULAR
      this.hiddenitem = true
    } else{
      this.hiddenitem = false;
    }
  }));

 }

}

