import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { JwtHelperService } from "@auth0/angular-jwt";
import { Usuario, UsuarioResponse } from 'src/models/login/usuarioLogin.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { decodetoken } from 'src/models/login/decodetoken.model';
import { ChangeEmailModel, ChangePassModel, ChangePhoneModel, ForgotPassModel } from 'src/models/login/login.model';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  ApiBase!:string ;
  ApiLogin!:string ;
  ApiLogout!:string ;
  ApiForgotPassword!:string ;
  ApiChangePassword!:string ;
  ApiValidateGoogle!:string ;
  ApiClientConfig!:string;
  ApiChangeEmail!:string;
  ApiChangePhone!:string;

 headers = new HttpHeaders({
  'Accept': 'application/json; odata=verbose',
  'Content-Type': 'application/json; charset=utf-8',
  'Access-Control-Allow-Origin':'*',
  'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
  });
  options = { headers: this.headers };


  constructor(private http:HttpClient,private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiLogin = this.settingsService.getapidatos().APIS.ApiLogin;
    this.ApiLogout = this.settingsService.getapidatos().APIS.ApiLogout;
    this.ApiChangePassword = this.settingsService.getapidatos().APIS.ApiChangePassword;    
    this.ApiForgotPassword = this.settingsService.getapidatos().APIS.ApiForgotPassword;
    this.ApiValidateGoogle = this.settingsService.getapidatos().APIS.ApiValidateGoogle;
    this.ApiClientConfig = this.settingsService.getapidatos().APIS.ApiClientConfig;
    this.ApiChangeEmail = this.settingsService.getapidatos().APIS.ApiChangeEmail;
    this.ApiChangePhone = this.settingsService.getapidatos().APIS.ApiChangePhone;
   }

  login(usuario:Usuario): Observable<UsuarioResponse>{
    return this.http.post<UsuarioResponse>(`${this.ApiBase}${this.ApiLogin}`, usuario,this.options).pipe(
      map((res: UsuarioResponse) => {
        return res;
      })
    )
  }

  ForgotPassword(usuario:ForgotPassModel): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiForgotPassword, usuario,this.options);
  }

  ChangePassword(usuario:ChangePassModel): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiChangePassword, usuario,this.options);
  }
  
  ChangeEmail(usuario:ChangeEmailModel): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiChangeEmail, usuario,this.options);
  }

  ChangePhone(usuario:ChangePhoneModel): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiChangePhone, usuario,this.options);
  }

  logout(): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiLogout, null,this.options);
  }
  
  setlocalStorage(data:any):void{
    
    localStorage.setItem('token',data);
  }

  getlocalStorageToken():string{
    return localStorage.getItem('token');
  }

  getTokenDecoded(): decodetoken{
    const helper = new JwtHelperService();
    const decodedToken = helper.decodeToken<decodetoken>(localStorage.getItem('token'));
    return decodedToken
  }

  removelocalStorage():void{
    localStorage.removeItem('token');
    localStorage.removeItem('theme');
    localStorage.removeItem('remember');

  }

  GetClientConfig(): Observable<ClientConfigResponse>{
    return this.http.get(`${this.ApiBase}${this.ApiClientConfig}`,this.options).pipe(
      map((res: ClientConfigResponse) => {
        return res;
      })
    );
  }



}
