import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription} from 'rxjs';
import { AmountModel, AmountModelResponse } from 'src/models/Portafolio/BuyAndSell/amount.model';
import { MaxModel, MaxModelResponse } from 'src/models/Portafolio/BuyAndSell/max.model';
import { QuantityModel, QuantityModelResponse } from 'src/models/Portafolio/BuyAndSell/quantity.model';
import { ClientService } from 'src/services/Client/client.service';
import { ManageDataService } from 'src/services/manage-data.service';
import { PaymentService } from 'src/services/payments/payment.service';
import { PortafolioService } from 'src/services/portafolio/portafolio.service';
import { SocketioService } from 'src/services/WebSocket/socketio.service';
import { BloxResponse } from 'src/models/WebSockets/Blox/bloxResponse.model';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { NgxSpinnerService } from 'ngx-spinner';
import { MatDialogRef } from '@angular/material/dialog';
import { BalanceForSymbolModel, BalanceForSymbolModelResponse } from 'src/models/Portafolio/portfolioBalanceForSymbol.model';
import { BuySellManageData } from 'src/models/Buy_Sell/BuySell.model';


@Component({
  selector: 'app-buy-sell',
  templateUrl: './buy-sell.component.html',
  styleUrls: ['./buy-sell.component.scss']
})
export class BuySellComponent implements OnInit, OnDestroy {

  @ViewChild('inputAmount') inputAmountElement!: ElementRef<HTMLInputElement>;
  @ViewChild('inputQuantity') inputQuantityElement!: ElementRef<HTMLInputElement>;

  assets_data: any;
  qty: any;
  qty_value: any;
  usd: any;
  usd_value: any;
  fee_value: any;
  fee: any;
  fees_client: any;
  total_value: any;
  amount_value: any = "";
  quantity_value: any = "";
  flagRequest: any = 'amount';
  portfolio_data: any;
  buymax_available: any = false;
  sellmax_available: any = true;
  amount_disabled: any = false;
  qty_disabled: any = true;
  validating: boolean = false;
  disable: any = false;
  max:boolean = false;
  actionToDo: string = "";

  ask_value: any;
  bid_value: any;
  symbol: any;
  price: any = 0;

  graphvalue_1 = []
  public subscription: Subscription = new Subscription();

  constructor(private client: ClientService, private portfolio_service: PortafolioService,
    private balance: PortafolioService, private toastr: ToastrService, private websocket_msg: SocketioService,
    private router: Router, private manage_data: ManageDataService<any>,private spinner:NgxSpinnerService, 
    private buy_sell: PaymentService, private dialogRef:MatDialogRef<BuySellComponent>) { }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  async ngOnInit() {
    this.spinner.show();
    await this.GetSubject();
  }


  async GetSubject() {

    this.subscription.add(this.manage_data.getSubject<BuySellManageData>(EnumEventsPopUp.buy_sell.toString()).subscribe(async (data: BuySellManageData) => {
      if (data) {
        await this.GetConfigData(data);
        this.buysell();
        this.spinner.hide();

      }
      else {
        this.ReturnToAssets();
      }
    }));

  }
  async GetDataBlox() {
    this.subscription.add(this.websocket_msg.GetDataBlox().subscribe(async(data: BloxResponse) => {
      try {
        if (typeof data === 'string') {
          this.SetValues(JSON.parse(data));
        }
        else {
          console.warn("wrong data websocket", data);
        }

      } catch (error) {
        console.error("error websocket GetDataBlox", error);
      }
    }))
  }

  async GetBalanceUSDResponse() {
    let balancesUSDResponse = await this.GetPortafolioBalanceBySymbol(this.portfolio_data.name, "USD");
    this.usd = Number(balancesUSDResponse).toFixed(3);
    this.usd_value = balancesUSDResponse;
    if (this.usd <= 0) {
      this.buymax_available = false;
    } else {
      this.buymax_available = true;
    }
  }

  async GetBalanceSymbolResponse(selldata) {
    let balanceSymbolResponse = await this.GetPortafolioBalanceBySymbol(this.portfolio_data.name, selldata);
    this.qty = Number(balanceSymbolResponse).toFixed(3);
    this.qty_value = balanceSymbolResponse;
    if (this.qty <= 0) {
      this.sellmax_available = false;
    } else {
      this.sellmax_available = true;
    }
  }

  async GetConfigData(buySellData: BuySellManageData) {

      this.symbol = buySellData.symbol;
      this.assets_data = buySellData;
      this.actionToDo = buySellData.action;

      let portafolioResponse = await this.GetDefaultPortafolio();
      let clientInfoResponse = await this.GetClientInfo();
      this.portfolio_data = portafolioResponse;
      this.fees_client = JSON.parse(clientInfoResponse.fees);

      await this.GetDataBlox();
      await this.GetBalanceUSDResponse();      
      await this.GetBalanceSymbolResponse(buySellData.symbol);     

      switch (buySellData.action) {
        case "sell":
          this.usd = Number(this.qty * this.bid_value).toFixed(3)
          this.fee = Number(this.fees_client.SELL);

          break;
        case "buy":
          this.qty = Number(this.usd / this.ask_value).toFixed(3)
          this.fee = Number(this.fees_client.BUY);
          break;
      }
      this.SetFocusOnElement();

  }

  SetValues(data: BloxResponse) {

    if (data.product_id == this.symbol + "-USD") {

      if (this.ask_value != data.best_ask.price) {
        this.ask_value = data.best_ask.price; //Comprar
      }

      if (this.bid_value != data.best_bid.price) {
        this.bid_value = data.best_bid.price; //Vender
      }

      if (this.actionToDo == 'buy' && this.price != this.ask_value) {
        this.price = this.ask_value;
      } else if (this.actionToDo == 'sell' && this.price != this.bid_value) {
        this.price = this.bid_value;
      }

    }

    return;
  }

  GetPortafolioBalanceBySymbol(portafolioName: string, symbol: string): Promise<any> {
    const asset: BalanceForSymbolModel = {
      portfolio_name: portafolioName,
      symbol: symbol
    }
    return new Promise((resolve, reject) => {
      this.subscription.add(this.balance.GetPortafolioBalancesForSymbol(asset).subscribe(data => {
        console.log("data.qty:", data);
        if (data) {
          resolve(data.qty);
        }
        else {
          resolve(0);
        }

      }, err => {
        console.error("Error on GetAssetData:", err);
        this.ReturnToAssets();
        resolve(null);
      }));
    });
  }





  InputAmount(value: string, isAmount: boolean) {
    this.max = false;
    if (isAmount) {
      if (this.actionToDo == "buy") {

        this.amount_value = Number(value);
        this.quantity_value = Number(this.amount_value / this.ask_value);
        this.fee_value = Number(this.amount_value / this.fee).toFixed(3);
        this.total_value = Number(this.amount_value + Number(this.fee_value)).toFixed(3);

      } else if (this.actionToDo == "sell") {

        this.amount_value = Number(value);
        this.quantity_value = Number(this.amount_value / this.bid_value);
        this.fee_value = Number(this.amount_value / this.fee).toFixed(3);
        this.total_value = Number(this.amount_value + Number(this.fee_value)).toFixed(3);

      }
    } else {
      if (this.actionToDo == "buy") {

        this.quantity_value = Number(value);
        this.amount_value = Number(this.quantity_value * this.ask_value).toFixed(3);
        this.fee_value = Number(this.amount_value / this.fee).toFixed(3);
        this.total_value = Number(Number(this.amount_value) + Number(this.fee_value)).toFixed(3);

      } else if (this.actionToDo == "sell") {

        this.quantity_value = Number(value);
        this.amount_value = Number(this.quantity_value * this.bid_value).toFixed(3);
        this.fee_value = Number(this.amount_value / this.fee).toFixed(3);
        this.total_value = Number(Number(this.amount_value) + Number(this.fee_value)).toFixed(3);

      }

    }


    if (this.actionToDo == "buy" && Number(this.total_value) > this.usd_value) {
      this.disable = true;
      this.ShowError("Available exceeded !!", "", false);
      return;
    }

    if (this.actionToDo == "sell" && this.quantity_value > this.qty_value) {
      this.disable = true;
      this.ShowError("Available exceeded !!", "", false);
      return;
    }

    if (Number(this.total_value) === 0) {
      this.disable = true;
      return;
    }

    this.disable = false;



  }

  async Buy() {
    this.validating = true;

    if(this.max){
      console.log("BuyMax")
      this.ActionBuyMax();
      return;
    }

    switch (this.flagRequest) {
      case 'qty':
        let buyByQtyResponse = await this.BuyByQty();
        if (!buyByQtyResponse) {
          this.validating = false;
          setTimeout(() => {
            this.amount_value = 0;
            this.quantity_value = 0;

          }, 3000);
          return;
        }
        console.log("buyByQtyResponse:", buyByQtyResponse);
        this.validating = false;
        this.Close();
        // this.router.navigate(['/home/portafolio']);

        break;

      case 'amount':
        let buyByAmountResponse = await this.BuyByAmount();
        if (!buyByAmountResponse) {
          this.validating = false;
          setTimeout(() => {
            this.amount_value = 0;
            this.quantity_value = 0;

          }, 3000);
          return;
        }
        console.log("buyByAmountResponse:", buyByAmountResponse);
        this.validating = false;
        this.Close();
        // this.router.navigate(['/home/portafolio']);


        break;

      default:
        break;
    }
  }

  async Sell() {
    this.validating = true;
    if(this.max){
      console.log("SellMax")
      this.ActionSellMax();
      return;
    }

    switch (this.flagRequest) {
      case 'qty':
        let sellByQtyResponse = await this.SellByQty();
        if (!sellByQtyResponse) {
          this.validating = false;
          setTimeout(() => {
            this.amount_value = 0;
            this.quantity_value = 0;

          }, 3000);
          return;
        }
        this.validating = false;
        this.Close();
        // this.router.navigate(['/home/portafolio']);
        break;

      case 'amount':
        let sellByAmountResponse = await this.SellByAmount();
        if (!sellByAmountResponse) {
          this.validating = false;
          setTimeout(() => {
            this.amount_value = 0;
            this.quantity_value = 0;

          }, 3000);
          return;
        }
        this.validating = false;
        this.Close();
        // this.router.navigate(['/home/portafolio']);
        break;

      default:
        break;
    }
  }

  async ActionBuyMax() {
    this.validating = true;
    const data: any = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD"
    }

    let buyMaxResponse = await this.BuyMax(data);
    if (buyMaxResponse === null) {
      this.validating = false;
      setTimeout(() => {
        this.amount_value = 0;
        this.quantity_value = 0;

      }, 3000);
      return;
    }

    this.Close();

  }

  async ActionSellMax() {
    this.validating = true;
    const data: any = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD"
    }

    let sellMaxResponse = await this.SellMax(data);
    if (sellMaxResponse === null) {
      this.validating = false;
      setTimeout(() => {
        this.amount_value = 0;
        this.quantity_value = 0;

      }, 3000);
      return;
    }

    this.Close();

  }

  PerceintsButtons(perceint: any) {

    switch (perceint) {
      case '25':
        this.max = false;
        this.ApplyPercentage(0.25);
        break;

      case '50':
        this.max = false;
        this.ApplyPercentage(0.5);
        break;

      case '75':
        this.max = false;
        this.ApplyPercentage(0.75);
        break;

      case '100':
        this.max = true;
        this.ApplyPercentage(1);
        break;

      default:
        break;
    }
  }

  BuyByQty(): Promise<QuantityModelResponse> {

    const data: QuantityModel = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD",
      quantity: Number(this.quantity_value)
    }

    return new Promise<QuantityModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.BuyByQty(data).subscribe(response => {
        resolve(response);
      }, err => {
        this.ReturnToAssets();
        resolve(null);
      }))
    })
  }

  SellByQty(): Promise<QuantityModelResponse> {

    const data: QuantityModel = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD",
      quantity: Number(this.quantity_value)
    }

    return new Promise<QuantityModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.SellByQty(data).subscribe(response => {
        resolve(response);
      }, err => {
        this.ReturnToAssets();
        reject(null);
      }))
    })
  }

  SellByAmount(): Promise<AmountModelResponse> {

    const data: AmountModel = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD",
      amount: Number(this.amount_value)
    }
    return new Promise<AmountModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.SellByAmount(data).subscribe(response => {
        resolve(response);
      }, err => {
        reject(null);
        this.ReturnToAssets();

      }))
    })
  }

  BuyByAmount(): Promise<AmountModelResponse> {

    const data: AmountModel = {
      portfolio_name: this.portfolio_data.name,
      user_oid: Date.now(),
      symbol: this.assets_data.symbol + "-USD",
      amount: Number(this.amount_value)
    }
    return new Promise<AmountModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.BuyByAmount(data).subscribe(response => {
        resolve(response);
      }, err => {
        this.ReturnToAssets();
        reject(null);
      }))
    })
  }

  BuyMax(data: MaxModel): Promise<MaxModelResponse> {
    return new Promise<MaxModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.BuyMax(data).subscribe(response => {
        resolve(response);
      }, err => {
        this.ReturnToAssets();
        resolve(null);
      }));
    });
  }

  SellMax(data: MaxModel): Promise<MaxModelResponse> {
    return new Promise<MaxModelResponse>((resolve, reject) => {
      this.subscription.add(this.buy_sell.SellMax(data).subscribe(response => {
        resolve(response);
      }, err => {
        resolve(null);
        this.ReturnToAssets();

      }));
    });
  }

  GetClientInfo() {
    return new Promise<ClientConfigResponse>((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((response:ClientConfigResponse) => {
        resolve(response);
      }, err => {
        resolve(null);
        this.ReturnToAssets();

      }));
    });
  }

  GetDefaultPortafolio(): Promise<PortafolioDefault>{
    return new Promise<PortafolioDefault>((resolve, reject) => {
      this.subscription.add(this.portfolio_service.GetDefault().subscribe(response=>{
        resolve(response);
      },err=>{
        console.error("Error on GetDefaultPortafolio:", err);
        resolve(null);
      }));
    });
  }

  buysell() {
    this.qty = Number(this.usd / this.ask_value).toFixed(3)
    this.fee = Number(this.fees_client.BUY);

  }

  BuyAction() {
    this.actionToDo = 'buy';
    this.qty = Number(this.usd / this.ask_value).toFixed(3)
    this.fee = Number(this.fees_client.BUY);
    this.SetFocusOnElement()

  }

  SellAction() {
    this.actionToDo = 'sell'
    this.usd = Number(this.qty * this.bid_value).toFixed(3)
    this.fee = Number(this.fees_client.SELL);
    this.SetFocusOnElement();
  }

  AmountAction() {
    this.flagRequest = 'amount';
    this.amount_disabled = false;
    this.qty_disabled = true;
    this.SetFocusOnElement();
  }

  QuantityAction() {
    this.flagRequest = 'qty';
    this.amount_disabled = true;
    this.qty_disabled = false;
    this.SetFocusOnElement();
  }

  ShowError(msg: string, title: string = "", disableTimeOut: boolean = true) {
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,
      positionClass: 'toast-center-center',
    });
  }

  ReturnToAssets(miliseconds: number = 3000) {
    setTimeout(() => {
      this.router.navigate(['/home/dashboard/assets']);
    }, miliseconds);
  }

  ReturnToPortafolio(miliseconds: number = 0) {
    setTimeout(() => {
      this.router.navigate(['/home/portafolio']);
    }, miliseconds);
  }

  SetFocusOnElement() {
    setTimeout(() => {
      if (this.flagRequest == "amount") {
        if (this.inputAmountElement) {
          this.inputAmountElement.nativeElement.focus();
        }
      }
      else {
        if (this.inputQuantityElement) {
          this.inputQuantityElement.nativeElement.focus();
        }
      }

    }, 100);
  }

  ApplyPercentage(percentage: number) {

    if (this.actionToDo == 'sell') {
      if (this.qty_value > 0) {
        this.quantity_value = (Number(this.qty_value) * percentage);
        this.amount_value = Number(this.quantity_value * this.bid_value).toFixed(3);
      }
      else {
        console.warn("there are not enough coins");
        this.disable = true;
        this.ShowError("Available exceeded !!", "", false);
        return;
      }
    } else if (this.actionToDo == 'buy') {
      if (this.usd_value > 0) {
        let valueFee = 0;
        if (percentage === 1) {
          valueFee = this.usd_value / this.fee;
        }
        this.amount_value = (Number(this.usd_value - valueFee) * percentage).toFixed(3);
        this.quantity_value = Number(this.amount_value / this.ask_value);
      }
      else {
        console.warn("there are not enough money");
        this.disable = true;
        this.ShowError("Available exceeded !!", "", false);
        return;
      }
    }
    this.fee_value = Number(this.amount_value / this.fee).toFixed(3);
    this.total_value = Number(Number(this.amount_value) + Number(this.fee_value)).toFixed(3);
    this.disable = false;

  }

  //********************************************************************************
  //Close PopUp
  //********************************************************************************
  
  Close(){
    this.dialogRef.close();
  }

}
