import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardGuard } from 'src/services/Guard/Dashboard.guard';
import { VigilanteGuard } from 'src/services/Guard/vigilante.guard';
import { BuySellComponent } from './BuyAndSell/buy-sell/buy-sell.component';
import { BaseLayoutComponent } from './layout/base-layout/base-layout.component';
import { GenerateQRComponent } from './Options/generate-qr/generate-qr.component';
import { TableInfoTransactionsComponent } from './Options/table-info-transactions/table-info-transactions.component';
import { ChangeEmailComponent } from './UserPages/change-email/change-email.component';
import { ChangePasswordComponent } from './UserPages/change-password/change-password.component';
import { ChangePhoneComponent } from './UserPages/change-phone/change-phone.component';
import { ForgotPasswordComponent } from './UserPages/forgot-password/forgot-password.component';
import { LoginComponent } from './UserPages/login/login.component';
import { RegisterComponent } from './UserPages/register/register.component';
import { ValidationComponent } from './UserPages/validation/validation.component';
import { VouchedComponent } from './UserPages/vouched/vouched.component';

const routes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'registration', component: RegisterComponent },
  { path: 'qr/:generate2fa', component: GenerateQRComponent }, 
  { path: 'vouched', component: VouchedComponent },
  { path: 'fogotpassword', component: ForgotPasswordComponent },
  { path: 'changepassword', component: ChangePasswordComponent },
  { path: 'changeemail', component: ChangeEmailComponent },
  { path: 'changephone', component: ChangePhoneComponent },
  { 
    path: 'validation',
    component: ValidationComponent, 
    canActivate: [VigilanteGuard] 
  },
  {
    path: 'home',
    canActivate: [DashboardGuard],
    component: BaseLayoutComponent,
    children: [
      { path: 'prueba', component: TableInfoTransactionsComponent },  
      
      {path: 'buy_sell', component:BuySellComponent},
      {
        path: 'dashboard',
        loadChildren: () => import('./Principal/principal.module').then(m => m.PrincipalModule)
      },
      {
        path: 'portafolio',
        loadChildren: () => import('./Portafolio/portafolio.module').then(m => m.PortafolioModule)
      },
      {
        path: 'payment_methods',
        loadChildren: () => import('./PaymentMethods/payment-methods.module').then(m => m.PaymentMethodsModule)
      },
      
      {path:'**', redirectTo: '/home/dashboard', pathMatch:'full'}

    ]
  },
  { path: '**', redirectTo: '/', pathMatch: 'full' }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
