import { Component, OnDestroy, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder,FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { Usuario, UsuarioResponse } from 'src/models/login/usuarioLogin.model';
import { LoginService } from 'src/services/login/login.service';
import { faLock} from '@fortawesome/free-solid-svg-icons';
import { PasswordStrengthValidator } from 'src/services/FormValidation/passwordStrength.validator';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy, AfterViewInit{

  @ViewChild('codeInput') codeInputElement!: ElementRef<HTMLInputElement>;
  loginForm: FormGroup;
  public subscription: Subscription = new Subscription();
  faLock = faLock;
  ErroresSpan: any = listadeErrores;
  version:string = window['version'];
  validating:boolean = false;
 
  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router, private toastr: ToastrService,private translate:TranslateService) {
    this.loginForm = this.fb.group({
      email: ['',Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
      ])],
      password: ['', Validators.compose([
        Validators.required,
        Validators.minLength(8),
        PasswordStrengthValidator
      ])],
      checkbox:[false]
    })
  }

  viewPassword(){
    const togglePassword = document.querySelector('#togglePassword');
    const password = document.querySelector('#password');

    togglePassword.addEventListener('click', function (e) {
      // toggle the type attribute
      const type = password.getAttribute('type') === 'password' ? 'text' : 'password';
      password.setAttribute('type', type);
      // toggle the eye / eye slash icon
      this.classList.toggle('bi-eye');
  });
  }



  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    console.log("Version ",this.version)
    if(localStorage.getItem('remember') === "true"){      
      this.router.navigate(['validation/']);   
    }else if(localStorage.getItem('remember')  === "false"){
      this.loginService.removelocalStorage();
    }
  }
  holamundo():string{
    return "hola mundo"
  }
  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }

  async Login(): Promise<void> {
    this.validating = true;
    const usuario: Usuario = {
      username: this.loginForm.value.email,
      password: this.loginForm.value.password
    }

    this.subscription.add(this.loginService.login(usuario).subscribe(async (data:UsuarioResponse) => {
      this.loginService.setlocalStorage(data.access_token);
      if(this.loginForm.value.checkbox){
        localStorage.setItem('remember',this.loginForm.value.checkbox);
      }else{
        localStorage.setItem('remember',this.loginForm.value.checkbox);
      }
      let msgstatus  = await this.translate.get("general.succes").toPromise();
      let msg  = await this.translate.get("login.code").toPromise();      
      this.toastr.success(msg,msgstatus)
      this.router.navigate(['validation/']);   
      this.validating = false;
    }, (error)=> {
      console.log("Error Login", error)
      this.loginForm.setValue({email: this.loginForm.value.email, password: '',checkbox: false})
      this.validating = false;
    }));


  }


}
