import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigurationModel } from 'src/models/Configuration/Configuration.model';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AppSettingsService {
  appConfig:any;
  constructor(private http :HttpClient) { }


  async loadSettings(){

    var ip = await  this.GetIP();
    if(environment.dev){
      return await this.http.get('assets/configuration/configuration.prod.json')
      .toPromise()
      .then(data => {
        JSON.stringify(data,(indice,valor:ConfigurationModel)=>{
          console.log("enviromenttt "+valor.APIS.ApiBase)

          if(valor.General.Ip === ""){
            valor.General.Ip = ip;
          }

        })
         this.appConfig = data;               
      });
    }else{
      return await this.http.get('assets/configuration/configuration.json')
      .toPromise()
      .then(data => {
        JSON.stringify(data,(indice,valor:ConfigurationModel)=>{
          if(valor.General.Ip === ""){
            valor.General.Ip = ip;
          }

        })
         this.appConfig = data;               
      });
    }

  }

  getapidatos():ConfigurationModel {
    
    if (!this.appConfig) {
      throw Error('Config file not loaded!');
    }

    return this.appConfig;
  }
 
  public GetIP():string {
    let ip = location.host;
    let n = ip.indexOf(":");

    if (n !== -1) {
        ip = ip.substring(0, n);
    }   
    
    return ip;
}
}
