import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ManageDataService } from 'src/services/manage-data.service';

@Component({
  selector: 'app-details-transactions-portfolio',
  templateUrl: './details-transactions-portfolio.component.html',
  styleUrls: ['./details-transactions-portfolio.component.scss']
})
export class DetailsTransactionsPortfolioComponent implements OnInit, OnDestroy {

  data:any;
  public subscription:Subscription = new Subscription();

  constructor(private manage_data: ManageDataService<any>, private dialogRef:MatDialogRef<DetailsTransactionsPortfolioComponent>) { }
  
  ngOnDestroy(): void {
    this.subscription.unsubscribe()
  }

  ngOnInit(): void {
    this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.DetailsPortfolio.toString()).subscribe((data:any)=>{
      this.data = data;
      this.VerifyData();
    }));
    
  }

  VerifyData(){
    if(this.data.transaction_type == 'SELL'){
      this.data.fee_amount = this.data.fee_amount * -1; 
    }
  }

  GetColor(value: any) {
    if (Number(value) < 0) {
      return 'red';
    } else {
      return 'green'
    }
  }

  Close(){
    this.dialogRef.close()
  }

}
