import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { ExhangeTimeModelResponse, GetCountriesModelResponse, GetCurrenciesModelResponse, GetLanguagesModelResponse } from 'src/models/RefData/refData.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class RefDataService {
  ApiBase!:string ;
  ApiCountries!:string ;
  ApiCurrencies!:string ;
  ApiLanguages!:string ;
  ApiExchangeTime!:string ;

  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };
  
    constructor(private http:HttpClient,private settingsService: AppSettingsService) { 
      this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
      this.ApiCountries = this.settingsService.getapidatos().APIS.ApiRefData.ApiCountries;
      this.ApiCurrencies = this.settingsService.getapidatos().APIS.ApiRefData.ApiCurrencies;
      this.ApiLanguages = this.settingsService.getapidatos().APIS.ApiRefData.ApiLanguages;
      this.ApiExchangeTime = this.settingsService.getapidatos().APIS.ApiRefData.ApiExchangeTime;

    }

    GetCountries(): Observable<GetCountriesModelResponse[]>{
      return this.http.post<GetCountriesModelResponse[]>(this.ApiBase +this.ApiCountries, null,this.options).pipe(
        map((res: GetCountriesModelResponse[]) => {
          return res;
        })
      );
    }

    GetCurrencies(): Observable<GetCurrenciesModelResponse[]>{
      return this.http.post<GetCurrenciesModelResponse[]>(this.ApiBase+this.ApiCurrencies , null,this.options).pipe(
        map((res: GetCurrenciesModelResponse[]) => {
          return res;
        })
      );
    }

    GetLanguages(): Observable<GetLanguagesModelResponse[]>{
      return this.http.post<GetLanguagesModelResponse[]>(this.ApiBase +this.ApiLanguages, null,this.options).pipe(
        map((res: GetLanguagesModelResponse[]) => {
          return res;
        })
      );
    }

    GetExhangeTime(): Observable<ExhangeTimeModelResponse>{
      return this.http.post<ExhangeTimeModelResponse>(this.ApiBase +this.ApiExchangeTime, null,this.options).pipe(
        map((res: ExhangeTimeModelResponse) => {
          return res;
        })
      );
    }


}
