import { Component, OnDestroy, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { UsuarioRegister } from 'src/models/register/register.model';
import { PasswordStrengthValidator } from 'src/services/FormValidation/passwordStrength.validator';
import { RefDataService } from 'src/services/RefData/ref-data.service';
import { RegisterService } from 'src/services/register/register.service';
import { faLock} from '@fortawesome/free-solid-svg-icons';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { GeneralResponse } from 'src/models/generalresponse.model';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('nameInput') codeInputElement!: ElementRef<HTMLInputElement>;

  registerForm:FormGroup;
  faLock = faLock;
  countries!:any;
  indicator!:any;
  domicile!:any;
  currency!:any;
  DateTime_init: string = '';
  listlanguage:any;
  language:string;
  public subscription: Subscription = new Subscription();
  ErroresSpan: any = listadeErrores;

  constructor(private translate:TranslateService,private fb:FormBuilder, private registerService:RegisterService,private router:Router,private refdataservice:RefDataService,private toastr:ToastrService) {

    this.registerForm = this.fb.group({
      name :['',Validators.required],
      lastname :['',Validators.required],
      countryselect:[null,Validators.required],
      phone :['',Validators.compose([
        Validators.required,
        Validators.minLength(8),
      ])],
      dob:['',Validators.required],
      email :['',Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
      ])],
      password:['', Validators.compose([
        Validators.required,
        Validators.minLength(8),
        PasswordStrengthValidator

      ])],
      passwordConfirmation :['',Validators.compose([
        Validators.required,
        Validators.minLength(8),
      ])],

    }, { 
      validators: this.passwordMatch.bind(this)
    })
   }


  passwordMatch(formGroup: FormGroup) {
    const { value: password } = formGroup.get('password');
    const { value: confirmPassword } = formGroup.get('passwordConfirmation');
    return password === confirmPassword ? null : { passwordNotMatch: true };
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.registerForm.reset();

  }


  ngOnInit(): void {
    this.GetCountries();
    this.Getlanguages();
    this.registerForm.reset();

  }

  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }


  onChanges(e) {
    this.indicator = this.registerForm.value.countryselect.phone_area_code
    this.domicile = this.registerForm.value.countryselect.gec
    this.currency = this.registerForm.value.countryselect.currency_id
  }

  GetCountries():void{
    this.subscription.add(this.refdataservice.GetCountries().subscribe(data=>{
      this.countries = data;
    }))

  }

  Getlanguages():void{
    this.subscription.add(this.refdataservice.GetLanguages().subscribe(data=>{
      this.listlanguage = data;
    }))

  }

  Register():void{
    let language_navigator =  window.navigator.language;

    this.listlanguage.forEach(element => {
      if(element.alpha2 !== null){
        if(language_navigator.split("-")[0] == element.alpha2){
          this.language = element.alpha3_b
        }
      }      
    });


    const usuarioregister:UsuarioRegister = {
      fname :this.registerForm.value.name,
      lname:this.registerForm.value.lastname,
      email:this.registerForm.value.email,
      phone:this.indicator + this.registerForm.value.phone,
      password:this.registerForm.value.password,
      dob:this.registerForm.value.dob,
      domicile:this.domicile,
      language:this.language,
      preferred_currency:this.currency,
      ref_code:this.registerForm.value.password,
    }       

    this.subscription.add(this.registerService.register(usuarioregister).subscribe(async (data:GeneralResponse<string>)=>{
      let msgstatus  = await this.translate.get("registration.registrationSuccess").toPromise();
      this.toastr.success(data.payload,msgstatus)
      this.router.navigate(['/'])
    }, error =>{
      this.registerForm.reset();
    }));

  
  }
}


