import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { ValidateModel, ValidateGoogleModelResponse, ValidateEmailModelResponse, ValidatePhoneModelResponse, ConfirmModelResponse, UpdateNameModel, ConfirmIDModel } from 'src/models/Validation/validation.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class ValidationService {

  ApiBase!:string ;
  ApiLogin!:string ;
  ApiValidateGoogle!:string ;
  ApiValidateSMS!:string ;
  ApiValidateEmail!:string ;
  ApiConfirmeEmail!:string ;
  ApiConfirmeSMS!:string ;
  ApiUpdateName!:string ;
  ApiJobID!:string ;


  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
  });
  options = { headers: this.headers };

  constructor(private http:HttpClient,private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiLogin = this.settingsService.getapidatos().APIS.ApiLogin;
    this.ApiValidateGoogle = this.settingsService.getapidatos().APIS.ApiValidateGoogle;
    this.ApiValidateSMS = this.settingsService.getapidatos().APIS.ApiValidateSms;
    this.ApiValidateEmail = this.settingsService.getapidatos().APIS.ApiValidateEmail;
    this.ApiConfirmeEmail = this.settingsService.getapidatos().APIS.ApiConfirmeEmail;
    this.ApiConfirmeSMS = this.settingsService.getapidatos().APIS.ApiConfirmeSMS;
    this.ApiUpdateName = this.settingsService.getapidatos().APIS.ApiUpdateName;
    this.ApiJobID = this.settingsService.getapidatos().APIS.ApiJobID;

   }

  validate2faGoogle(code:ValidateModel): Observable<ValidateGoogleModelResponse>{
    return this.http.post<ValidateGoogleModelResponse>(this.ApiBase + this.ApiValidateGoogle, code,this.options).pipe(
      map((res: ValidateGoogleModelResponse) => {
        return res;
      })
    );
  }

  validate2faSMS(code:ValidateModel): Observable<ValidatePhoneModelResponse>{
    return this.http.post<ValidatePhoneModelResponse>(this.ApiBase + this.ApiValidateSMS, code,this.options).pipe(
      map((res: ValidatePhoneModelResponse) => {
        return res;
      })
    );
  }
  
  validate2faEmail(code:ValidateModel): Observable<ValidateEmailModelResponse>{
    return this.http.post<ValidateEmailModelResponse>(this.ApiBase + this.ApiValidateEmail, code,this.options).pipe(
      map((res: ValidateEmailModelResponse) => {
        return res;
      })
    );
  }

  ConfirmEmail(): Observable<ConfirmModelResponse>{
    return this.http.post<ConfirmModelResponse>(this.ApiBase + this.ApiConfirmeEmail, null,this.options).pipe(
      map((res: ConfirmModelResponse) => {
        return res;
      })
    );
  }

  ConfirmCelular(): Observable<ConfirmModelResponse>{
    return this.http.post(this.ApiBase + this.ApiConfirmeSMS, null,this.options).pipe(
      map((res: ConfirmModelResponse) => {
        return res;
      })
    );
  }

  UpdateName(data:UpdateNameModel): Observable<ConfirmModelResponse>{
    return this.http.post<ConfirmModelResponse>(this.ApiBase + this.ApiUpdateName, data,this.options).pipe(
      map((res: ConfirmModelResponse) => {
        return res;
      })
    );
  }

  ConfirmID(code:ConfirmIDModel): Observable<any>{
    return this.http.post(this.ApiBase + this.ApiJobID, code,this.options);
  }

}
