import { Component, OnDestroy, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { LoginService } from 'src/services/login/login.service';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { TranslateService } from '@ngx-translate/core';
import { ForgotPassModel } from 'src/models/login/login.model';
@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('codeInput') codeInputElement!: ElementRef<HTMLInputElement>;

  FogotForm: FormGroup;
  public subscription: Subscription = new Subscription();
  ErroresSpan: any = listadeErrores;
  flag:boolean = false;

  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router, private toastr: ToastrService,private translate:TranslateService) {
    this.FogotForm = this.fb.group({
      email: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
      ])],
    })
  }
  ngOnInit(): void {
  }
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }

  fogotPassword():void{
    this.flag = true;

    const usuario: ForgotPassModel = {
      email: this.FogotForm.value.email,
    }
    this.subscription.add(this.loginService.ForgotPassword(usuario).subscribe(async data=>{
      this.router.navigate(['/']);   
      let msg = await this.translate.get("ChangePassword.SendEmail").toPromise();
      let msgstatus  = await this.translate.get("general.succes").toPromise();
      this.toastr.success(msg+usuario.email,msgstatus)
    },error =>{
      this.flag = false;

    }))
    
  }
}
