import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { UsuarioRegister } from 'src/models/register/register.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';
import { map, Observable } from 'rxjs';
import { GeneralResponse } from 'src/models/generalresponse.model';

@Injectable({
  providedIn: 'root'
})
export class RegisterService {
  ApiBase!:string ;
  ApiRegister!:string ;
  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };
  
  constructor(private http:HttpClient,private settingsService: AppSettingsService) { 
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiRegister = this.settingsService.getapidatos().APIS.ApiRegister;
  }

  register(usuario:UsuarioRegister): Observable<GeneralResponse<string>>{
    return this.http.post(this.ApiBase +this.ApiRegister, usuario,this.options).pipe(
      map((res: GeneralResponse<string>) => {
        return res;
      }))
  }
}
