import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { ValidateModel } from 'src/models/Validation/validation.model';
import { ValidationService } from 'src/services/validation/validation.service';

@Component({
  selector: 'app-confirm-validate-google-qr',
  templateUrl: './confirm-validate-google-qr.component.html',
  styleUrls: ['./confirm-validate-google-qr.component.scss']
})
export class ConfirmValidateGoogleQRComponent implements OnInit, AfterViewInit {

  @ViewChild('codeInput') codeInputElement!: ElementRef<HTMLInputElement>;
  
  CodeForm: FormGroup;


  constructor(private dialogRef:MatDialogRef<ConfirmValidateGoogleQRComponent>, private validationservice: ValidationService,
    private translate:TranslateService, private toastr:ToastrService, private fb: FormBuilder) {
      
      this.CodeForm = this.fb.group({
        code: ['', Validators.compose([
          Validators.required,
          Validators.minLength(6),
        ])],
      })
     }


  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }

  ngOnInit(): void {
  }

  InputCode(code:string){
    if(code.length == 6){
      this.Code();
    }
  }

  Code(){
    const code: ValidateModel = {
      code: this.CodeForm.value.code,
    }
    this.ValidateGoogle(code);
  }

  ValidateGoogle(code:ValidateModel){
    this.validationservice.validate2faGoogle(code).subscribe(async data => {
      if (data.success) {
        let msgstatus = await this.translate.get("general.succes").toPromise();
        let msg = await this.translate.get("validation.Googlevalidation").toPromise();
        this.toastr.success(msg, msgstatus);
        this.dialogRef.close(EnumConfirmation.success.toString());
      } else {
        let msgstatus = await this.translate.get("general.error").toPromise();
        let msg = await this.translate.get("validation.ErrorCode").toPromise();
        this.toastr.error(msg, msgstatus, {
          closeButton: true,
          disableTimeOut: true,
          positionClass: 'toast-center-center',

        })
        this.CodeForm.reset();
      }
    }, error => {
      this.CodeForm.reset();
    });

  }

  Back(){
    this.dialogRef.close();
  }

}
