import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ConfirmationComponent } from 'src/app/PopUp/confirmation/confirmation.component';
import { QrViewComponent } from 'src/app/PopUp/qr-view/qr-view.component';
import { AboutComponent } from 'src/app/Principal/about/about.component';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { GenerateQRService } from 'src/services/GenerateQR/generate-qr.service';
import { LoginService } from 'src/services/login/login.service';
import { faCircleExclamation, faEllipsisV } from '@fortawesome/free-solid-svg-icons';
import { confirm } from 'src/models/Confirm/confirm.model';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  public isCollapsed = true;
  public subscription: Subscription = new Subscription();
  confirmidoption:boolean= false;
  constructor(private translate: TranslateService,private manage_data: ManageDataService<any>, private generateqrservice: GenerateQRService, private dialog: MatDialog, private loginService: LoginService, private router: Router, private toastr: ToastrService) { }
  
  async ngOnInit() {
    await this.configtheme();
    await this.needConfirmID();    

  }
  faCircleExclamation = faCircleExclamation;
  faEllipsisV = faEllipsisV;

  async logout() {
    this.loginService.logout().toPromise();
    this.loginService.removelocalStorage();
    this.router.navigate(['/']);
    let msg = await this.translate.get('general.sessionEnded').toPromise();
    this.toastr.success(msg);
  }

  async needConfirmID(){
    var data = await this.loginService.GetClientConfig().toPromise();
      if (data["id_verified"] === false && data["kyc_completed"] === false) {
        this.confirmidoption = true;
        // var msgstatus = await this.translate.get('settings.idVerify').toPromise()
        // this.toastr.error(msgstatus)
      }
      else{
        this.confirmidoption = false;
      }

  }


  async ConfirmQR(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";
    var data = {
      title: await this.translate.get('dialog.generate').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.sureGenerateCode').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.GenerateQRGoogle();
      }

    }));
  }


  async GenerateQRGoogle(): Promise<void> {
    var msg = await this.translate.get('Errores.errorQr').toPromise()
    this.generateqrservice.generateQR2faGoogle().subscribe(data => {
      this.SendatosQR(data);
    }, error => {
      console.log(msg + JSON.stringify(error))
      this.toastr.error(error.error.payload, msg)

    });
  }

  SendatosQR(data: any): void {
    this.manage_data.emitEvent<any>(EnumEventsPopUp.QR.toString(), data)
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    let dialogRef = this.dialog.open(QrViewComponent, dialogConfig);

  }

  ChangeLanguage() {
    console.log("traductor " + this.translate.currentLang)
    if (this.translate.currentLang == "es") {
      this.translate.use("en");
    } else {
      this.translate.use("es");
    }
  }


  async ChangePassword() {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    var data = {
      title: await this.translate.get('dialog.changePass').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.sureChangePass').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/changepassword']);
      }

    }));

  }

  async ChangeEmail() {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    var data = {
      title: await this.translate.get('dialog.changeEmail').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.sureChangeEmail').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/changeemail']);
      }

    }));

  }

  async ChangePhone() {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    var data = {
      title: await this.translate.get('dialog.changePhone').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.sureChangePhone').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/changephone']);
      }

    }));

  }

  ConfirmID(): void {
    this.router.navigate(['/vouched']);
  }


  About(): void {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    let dialogRef = this.dialog.open(AboutComponent, dialogConfig);

  }

  setThema() {
    if (localStorage.getItem("theme") === "dark") {
      localStorage.setItem("theme", "ligth")
    } else {
      localStorage.setItem("theme", "dark")
    }
    this.manage_data.emitEvent<string>(EnumEventsPopUp.ChangeColor.toString(),"change");
    this.configtheme();
  }

  async configtheme() {
    document.documentElement.className = localStorage.getItem("theme");
  }

}
