import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { ChangePhoneModel } from 'src/models/login/login.model';
import { ClientService } from 'src/services/Client/client.service';
import { LoginService } from 'src/services/login/login.service';
import { RefDataService } from 'src/services/RefData/ref-data.service';


@Component({
  selector: 'app-change-phone',
  templateUrl: './change-phone.component.html',
  styleUrls: ['./change-phone.component.scss']
})
export class ChangePhoneComponent implements OnInit, OnDestroy {

  loginForm: FormGroup;
  public subscription: Subscription = new Subscription();
  ErroresSpan: any = listadeErrores;
  countries:any;
  indicator: any;
  current_phone:string;
  validating:boolean = false;

  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router,
    private toastr: ToastrService,private translate:TranslateService, private refdataservice:RefDataService,
    private client:ClientService) {
    
    this.loginForm = this.fb.group({
      new_phone :['',Validators.compose([
        Validators.required,
        Validators.minLength(8),
      ])],
      confirmation_phone:['',Validators.compose([
        Validators.required,
        Validators.minLength(8),
      ])],
      countryselect:[null,Validators.required]
    })
   }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  async ngOnInit(){
    let data = await this.GetClientConfig()
    this.current_phone = data.phone;
    this.GetCountries();
  }

  async UpdatePhone(){
    this.validating = true;
    let newphone:string = this.indicator + this.loginForm.value.new_phone;
    let confirmnewphone:string = this.indicator + this.loginForm.value.confirmation_phone;

    console.log("Nuevo numero",newphone)
    console.log("Confirmacion numero",confirmnewphone)

    
    if(newphone.trim() == confirmnewphone.trim()){

      if(newphone.trim() == this.current_phone.trim()){
        this.validating = false;
        let title = await this.translate.get('general.error').toPromise();
        let msg = await this.translate.get('ChangePhone.errorPhoneSame').toPromise();
        this.ShowError(msg, title);
        return;  
      }

      const usuario: ChangePhoneModel = {
        new_phone: newphone
      }
  
      this.subscription.add(this.loginService.ChangePhone(usuario).subscribe(async data => {
        console.log("Change Phone Response", data);
        this.router.navigate(['validation/']);  
        let msg = await this.translate.get("ChangePhone.changePhone").toPromise();
        let msgstatus  = await this.translate.get("general.succes").toPromise();
        this.toastr.success(msg,msgstatus)
        this.validating = false;
        // this.toastr.success("Correo Cambiado!!")
  
      }, error => {
        this.validating = false;
        this.loginForm.reset();
      }));
      
    }else{
      this.validating = false;
      let title = await this.translate.get('general.error').toPromise();
      let msg = await this.translate.get('ChangePhone.errorConfirm').toPromise();
      this.ShowError(msg, title);
      return;
    }
    
  }

  onChanges(e) {
    this.indicator = this.loginForm.value.countryselect.phone_area_code
  }

  //===========================================================================
  // Show Error
  //===========================================================================

  ShowError(msg: string, title: string = "", disableTimeOut: boolean = true) {
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,
      positionClass: 'toast-center-center',
    });
  }

  //***************************************************************************************
  //Services
  //***************************************************************************************

  GetCountries():void{
    this.subscription.add(this.refdataservice.GetCountries().subscribe(data=>{
      this.countries = data;
    }))

  }

  GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise<ClientConfigResponse>((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }

}
