import { Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  public sizeDisplay: string = 'phone' || 'web';
  sildebar: boolean = true;
  porfolio: boolean = false;
  constructor( public breakpointObserver: BreakpointObserver) { this.mediaQuery();}

  ngOnInit(): void {
  }


  change(){
  this.sildebar = false
  }

  refre(){
    this.sildebar = true
    }

    drowport(){
      this.porfolio = !this.porfolio
    }


    public mediaQuery() {

      this.breakpointObserver
      .observe(Breakpoints.Small)
      .subscribe((state: BreakpointState) => {
        if (state.matches) {
        //AQUI SERA TRUE SOLO SI ESTA EN RESOLUCION DE CELULAR
        this.sildebar = false
      }
    });
  
    this.breakpointObserver
    .observe(Breakpoints.Web)
    .subscribe((state: BreakpointState) => {
      if (state.matches) {
        //AQUI SERA TRUE SOLO SI ES RESOLUCION PARA WEB
        this.sildebar = true
      }
    });
   }

   scrollToTop(): void {
    document.getElementById("scrollay").scrollIntoView(); 
  }

}
