import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { AmountModel, AmountModelResponse } from 'src/models/Portafolio/BuyAndSell/amount.model';
import { QuantityModel, QuantityModelResponse } from 'src/models/Portafolio/BuyAndSell/quantity.model';
import { MaxModel, MaxModelResponse } from 'src/models/Portafolio/BuyAndSell/max.model';
import { AppSettingsService } from 'src/services/AppSettingsInit/app-settings.service';
import { CashModel, CashModelResponse } from 'src/models/PaymentMethods/cash.model';
import { CardModel } from 'src/models/PaymentMethods/card.model';
import { BankModel } from 'src/models/PaymentMethods/bank.model';
import { DeleteModel } from 'src/models/PaymentMethods/delete.model';
import { DefaultModel } from 'src/models/PaymentMethods/default.model';
import { PaymentMethodsProvidersModel, PaymentMethodsProvidersModelResponse } from 'src/models/PaymentMethods/paymentMethodsProviders.model';
import { clientPayMethod } from 'src/models/PaymentMethods/clientPayMethods.model';

@Injectable({
  providedIn: 'root'
})
export class PaymentService {
  ApiBase!:string ;
  ApiConvertToUSD!:string ;
  ApiConvertFromUSD!:string ;
  ApiGetClientPaymentMethods!:string ;
  ApiBuyByAmount!:string ;
  ApiBuyByQty!:string ;
  ApiSellByQty!:string ;
  ApiSellByAmount!:string ;
  ApiBuyMax!: string;
  ApiSellMax!: string;
  ApiGetAllPaymentsProvidersByCountry!:string;
  ApiSaveBankPaymentMethod!:string;
  ApiSaveCardPaymentMethod!:string;
  ApiSaveCashPaymentMethod!:string;
  ApiDeleteClientPaymentMethod!:string;
  ApiSetDefaultPaymentMethod!:string;


  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };

  constructor(private http:HttpClient,private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiConvertToUSD = this.settingsService.getapidatos().APIS.ApiConvertToUSD;
    this.ApiConvertFromUSD = this.settingsService.getapidatos().APIS.ApiConvertFromUSD;
    this.ApiGetClientPaymentMethods = this.settingsService.getapidatos().APIS.ApiGetClientPaymentMethods;
    this.ApiBuyByAmount = this.settingsService.getapidatos().APIS.ApiBuyByAmount;
    this.ApiBuyByQty = this.settingsService.getapidatos().APIS.ApiBuyByQty;
    this.ApiBuyMax = this.settingsService.getapidatos().APIS.ApiBuyMax;
    this.ApiSellMax = this.settingsService.getapidatos().APIS.ApiSellMax;
    this.ApiSellByAmount = this.settingsService.getapidatos().APIS.ApiSellByAmount;
    this.ApiSellByQty = this.settingsService.getapidatos().APIS.ApiSellByQty;
    this.ApiGetAllPaymentsProvidersByCountry = this.settingsService.getapidatos().APIS.ApiGetAllPaymentsProvidersByCountry;
    this.ApiSaveBankPaymentMethod = this.settingsService.getapidatos().APIS.ApiSaveBankPaymentMethod;
    this.ApiSaveCardPaymentMethod = this.settingsService.getapidatos().APIS.ApiSaveCardPaymentMethod;
    this.ApiSaveCashPaymentMethod = this.settingsService.getapidatos().APIS.ApiSaveCashPaymentMethod;
    this.ApiDeleteClientPaymentMethod = this.settingsService.getapidatos().APIS.ApiDeleteClientPaymentMethod;
    this.ApiSetDefaultPaymentMethod = this.settingsService.getapidatos().APIS.ApiSetDefaultPaymentMethod;


  }

  SellMax(data:MaxModel): Observable<MaxModelResponse>{
    return this.http.post<MaxModelResponse>(this.ApiBase+this.ApiSellMax,data,this.options).pipe(
      map((res: MaxModelResponse) => {
        return res;
      }));
  }

  BuyMax(data:MaxModel): Observable<MaxModelResponse>{
    return this.http.post<MaxModelResponse>(this.ApiBase+this.ApiBuyMax,data,this.options).pipe(
      map((res: MaxModelResponse) => {
        return res;
      }));
  }
  
  GetClientPaymentMethods(data:any): Observable<clientPayMethod[]>{
    return this.http.post<clientPayMethod[]>(this.ApiBase+this.ApiGetClientPaymentMethods,data,this.options).pipe(
      map((res: clientPayMethod[]) => {
        return res;
      }));
  }

  BuyByQty(data:QuantityModel): Observable<QuantityModelResponse>{
    return this.http.post<QuantityModelResponse>(this.ApiBase+this.ApiBuyByQty,data,this.options).pipe(
      map((res: QuantityModelResponse) => {
        return res;
      }))
  }
  BuyByAmount(data:AmountModel): Observable<AmountModelResponse>{
    return this.http.post<AmountModelResponse>(this.ApiBase+this.ApiBuyByAmount,data,this.options).pipe(
      map((res: AmountModelResponse) => {
        return res;
      }))
  }
  SellByQty(data:QuantityModel): Observable<QuantityModelResponse>{
    return this.http.post<QuantityModelResponse>(this.ApiBase+this.ApiSellByQty,data,this.options).pipe(
      map((res: QuantityModelResponse) => {
        return res;
      }))
  }
  SellByAmount(data:AmountModel): Observable<AmountModelResponse>{
    return this.http.post<AmountModelResponse>(this.ApiBase+this.ApiSellByAmount,data,this.options).pipe(
      map((res: AmountModelResponse) => {
        return res;
      }))
  }

  GetAllPaymentsProvidersByCountry(data:PaymentMethodsProvidersModel): Observable<PaymentMethodsProvidersModelResponse[]>{
    return this.http.post<PaymentMethodsProvidersModelResponse[]>(this.ApiBase+this.ApiGetAllPaymentsProvidersByCountry,data,this.options).pipe(
      map((res: PaymentMethodsProvidersModelResponse[]) => {
        return res;
      }))
  }
  SaveBankPaymentMethod(data:BankModel): Observable<any>{
    return this.http.post(this.ApiBase+this.ApiSaveBankPaymentMethod,data,this.options)
  }
  SaveCardPaymentMethod(data:CardModel): Observable<any>{
    return this.http.post(this.ApiBase+this.ApiSaveCardPaymentMethod,data,this.options)
  }
  SaveCashPaymentMethod(data:CashModel): Observable<CashModelResponse>{
    return this.http.post<CashModelResponse>(this.ApiBase+this.ApiSaveCashPaymentMethod,data,this.options).pipe(
      map((res: CashModelResponse) => {
        return res;
      }))
  }
  DeleteClientPaymentMethod(data:DeleteModel): Observable<any>{
    return this.http.post(this.ApiBase+this.ApiDeleteClientPaymentMethod,data,this.options)
  }
  SetDefaultPaymentMethod(data:DefaultModel): Observable<any>{
    return this.http.post(this.ApiBase+this.ApiSetDefaultPaymentMethod,data,this.options)
  }
}
