import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';
import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppSettingsService } from 'src/services/AppSettingsInit/app-settings.service';
import { LoginService } from 'src/services/login/login.service';
import { SocketioService } from 'src/services/WebSocket/socketio.service';

@Component({
  selector: 'app-base-layout',
  templateUrl: './base-layout.component.html',
  styleUrls: ['./base-layout.component.scss']
})
export class BaseLayoutComponent implements OnInit, OnDestroy {
  public sizeDisplay: string = 'phone' || 'web';
  sildebar: boolean = true;
  porfolio: boolean = false;
  public subscription: Subscription = new Subscription();
  localTimer!: number;
  countRefresh = 0;
  isActivity = false;
  isMouseMove: any;
  scrollbtn:boolean = false;
  constructor(private loginService: LoginService, private router: Router,private settingsService: AppSettingsService, private translate:TranslateService, private websocket:SocketioService, private toastr: ToastrService,public breakpointObserver: BreakpointObserver) {this.mediaQuery(); }
  
  
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    clearTimeout(this.isMouseMove);

  }

  ngOnInit(): void {
    this.websocket.InitBloxOrder();
    this.GetNotificate();
    this.getTimeOutLogout();

  }

  
  getTimeOutLogout(){
    let timeZone;
    if (typeof Intl === 'object' && typeof Intl.DateTimeFormat === 'function') {
      timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    }

    var timeexp = this.loginService.getTokenDecoded();
    if(localStorage.getItem('remember') === "true"){      
      var timeexp = this.loginService.getTokenDecoded();
      var primero =  new Date(timeexp.exp*1000);
      var segundo = new Date(timeexp.iat*1000);
      var difference= Math.abs(Number(primero) - Number(segundo));
      var days = difference/(1000 * 3600 * 24)    
      if(days === 0){
        this.loginService.removelocalStorage();
        this.router.navigate(["/"])
      }
      clearTimeout(this.isMouseMove);

    }else if(localStorage.getItem('remember')  === "false"){
      this.localTimer = this.settingsService.getapidatos().Configuration.TimeExpiredSession;
      this.countDown();
    }

  }
  /**
   * Evento que escucha cuando el mouse se encuentra en movimiento
   * @param {MouseEvent} e - evento cuando el mouse esta en movimiento
   */
   @HostListener('document:mousemove', ['$event']) 
   onMosusemove(e: MouseEvent) {
    if(localStorage.getItem('remember') === "false"){ 
      this.countDown();
    }else if (localStorage.getItem('remember') === "true"){
      clearTimeout(this.isMouseMove);
    }
    this.changescroll()
   }
 
  async countDown() {
    this.isActivity = true;

    clearTimeout(this.isMouseMove);

    this.isMouseMove = setTimeout(() => {
      this.countRefresh = 0;
      this.isActivity = false;
      if (localStorage.getItem('token') != null) {
        this.loginService.logout().subscribe(async data => {
          var msg = await this.translate.get('Errores.EndSessionDetail').toPromise();
          var msg_title = await this.translate.get('Errores.EndSession').toPromise();
          this.toastr.error(msg, msg_title)
          this.loginService.removelocalStorage();
          this.router.navigate(["/"])
        }, error => {
          this.loginService.removelocalStorage();
          this.router.navigate(["/"])
        })
      }

    }, this.localTimer);

    if (this.isActivity && this.countRefresh === 1) {
      // se refresca sesion
    }
    this.countRefresh++;
  }



  async GetNotificate(){
    this.subscription.add(this.websocket.GetDataBloxOrder().subscribe(async (data:any)=>{
      if(typeof data != 'object' && data != "Hey Dude!"){
        if(JSON.parse(data).success == null || JSON.parse(data).success == undefined){  
          console.log("WebSocket Order",data)
          var borar = data
          await this.ResponseEvent(JSON.parse(borar))
        }
      } 	      
    }))
  }


  
  async ResponseEvent(message:any){
    switch (message.header.msg_type) {
      case 'ORDER_NEW':
        this.toastr.success("Order created");
        break;

      case 'ORDER_UPDATE':
        this.toastr.success("Order updated");        
        break;

      case 'ORDER_ERROR':
        var message2 = await this.translate.get('Errores.orderError').toPromise(); 
        this.ShowError(message.data.order_state, message2);        
        break;
      
      case 'ORDER_CANCEL':
        var message2 = await this.translate.get('Errores.orderCancel').toPromise(); 
        this.ShowError(message.data.order_state, message2);
        break;
      
      case 'ORDER_REJECTED':
        var message2 = await this.translate.get('Errores.orderRejected').toPromise(); 
        this.ShowError(message.data.order_state, message2);

        break;
        
      case 'EXECUTION':
        this.toastr.success("Succesful transaction!!!");
        break;

      case 'TRACKING_EXECUTION':
        this.toastr.success("Succesful tracking execution!!!");
        break;

      case 'DEPOSIT':
        this.toastr.success("Succesful deposit!!!");
        break;
      
      case 'WITHDRAW':
        this.toastr.success("Succesful withdraw!!!");
        break;
        
      default:
        break;
    }
  }

  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }
  


  public mediaQuery() {
    this.subscription.add(this.breakpointObserver
    .observe(Breakpoints.XSmall)
    .subscribe((state: BreakpointState) => {
      if (state.matches) {
      //AQUI SERA TRUE SOLO SI ESTA EN RESOLUCION DE CELULAR
      this.sildebar = false
    } else{
      this.sildebar = true;
    }
  }));

 }

 contorlmenu(){
  this.sildebar = !this.sildebar
 }



 changescroll(){
  if(document.getElementById("scrolltop").scrollTop>50){
    this.scrollbtn = true
  }else{
    this.scrollbtn = false
  }
  
 }
}
