import { Component, OnDestroy, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ConfirmationGenerateQrComponent } from 'src/app/PopUp/confirmation-generate-qr/confirmation-generate-qr.component';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { EnumAuthentication, EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { LoginService } from 'src/services/login/login.service';
import { ValidationService } from 'src/services/validation/validation.service';
import { ConfirmationComponent } from '../../PopUp/confirmation/confirmation.component';
import { confirm } from 'src/models/Confirm/confirm.model';
import { TranslateService } from '@ngx-translate/core';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ValidateModel } from 'src/models/Validation/validation.model';


@Component({
  selector: 'app-validation',
  templateUrl: './validation.component.html',
  styleUrls: ['./validation.component.scss']
})
export class ValidationComponent implements OnInit, OnDestroy, AfterViewInit {

  @ViewChild('codeInput') codeInputElement!: ElementRef<HTMLInputElement>;

  CodeForm: FormGroup;
  Confirm: string = "";
  title: string = "";
  Activate: boolean = false;
  Activatechange: boolean = false;
  initTemp: boolean = false;
  notconfirm: boolean = false;
  qr: boolean = false;
  onlyOneTime: boolean = false;
  flagtitle: boolean = false;
  verify: boolean = false;

  intervalId = 0;
  message = '';
  seconds = 15;

  public subscription: Subscription = new Subscription();

  constructor(private translate: TranslateService, private manage_data: ManageDataService<any>, private dialog: MatDialog,
    private loginService: LoginService, private fb: FormBuilder, private router: Router, private validationservice: ValidationService, 
    private toastr: ToastrService) {

    this.CodeForm = this.fb.group({
      code: ['', Validators.compose([
        Validators.required,
        Validators.minLength(6),
      ])],
    })

    this.ChangedInput();
  }


  ChangedInput() {
    this.subscription.add(this.CodeForm.valueChanges.subscribe(value => {

      if (this.Confirm == EnumAuthentication.google.toString()) {
        if (value.code != null) {
          if (value.code.length == 6) {
            this.LoginCode();
          }
        }

      }
      if (this.Confirm == EnumAuthentication.sms.toString() || this.Confirm == EnumAuthentication.email.toString()) {
        if (value.code != null) {

          if (value.code.length == 8) {
            this.LoginCode();
          }
        }

      }

    }))
  }



  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
  ngOnInit(): void {
    this.GetConfigClient();
  }
  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }

  LoginCode(): void {
    const Code: ValidateModel = {
      code: this.CodeForm.value.code,
    }
    this.Activatechange = true;
    console.log("llego aqui", this.Confirm)

    if (this.Confirm == EnumAuthentication.google.toString()) {
      this.validateGoogle(Code);
    }
    if (this.Confirm == EnumAuthentication.sms.toString()) {
      this.validateSMS(Code);
    }
    if (this.Confirm == EnumAuthentication.email.toString()) {
      this.validateEmail(Code);
    }


  }

  SendCode(): void {
    if (this.onlyOneTime) {
      return;
    }
    this.onlyOneTime = true;
    this.countDownTimmer();

    if (this.Confirm == EnumAuthentication.sms.toString()) {
      this.CodeForm.reset();
      this.ConfirmSMS();

    }
    if (this.Confirm == EnumAuthentication.email.toString()) {
      this.CodeForm.reset();
      this.ConfirmEmail();

    }
    this.onlyOneTime = false;
  }

  clearTimer() {
    clearInterval(this.intervalId);
  }

  countDownTimmer() {
    this.initTemp = true;
    this.clearTimer();
    this.intervalId = window.setInterval(() => {
      this.seconds -= 1;
      if (this.seconds === 0) {
        this.clearTimer();
        this.initTemp = false;

      } else {
        if (this.seconds < 0) {
          this.seconds = 15;
        } // reset
      }
    }, 1000);
  }

  AuthPending(data: ClientConfigResponse) {

    if (data["required_2fa_2proceed"] === EnumAuthentication.sms.toString()) {
      this.notconfirm = true;
      this.Activate = false;
      this.flagtitle = false;
      this.title = "SMS";
      this.Confirm = EnumAuthentication.sms.toString();
      this.ConfirmSMS();
    }

    if (data["required_2fa_2proceed"] === EnumAuthentication.none.toString()) {

      if (data["client_status"] === "PENDING_VERIFICATION" || this.verify) {
        this.Activate = false;

        if (data["phone_validated"] == false) {
          this.notconfirm = true;
          this.title = "SMS";
          this.flagtitle = false;
          this.Confirm = EnumAuthentication.sms.toString();
          this.ConfirmSMS();
        }
        else if (data["email_validated"] == false) {
          this.notconfirm = true;
          this.title = "Email";
          this.flagtitle = false;
          this.Confirm = EnumAuthentication.email.toString();
          this.ConfirmEmail();
        }
        else if (data["id_verified"] == false) {
          this.ConfirmID();
        }
        else if (data["kyc_completed"] == false) {
          this.ConfirmID();
        }
      }

      if (data["client_status"] === "ACTIVE" && !this.verify) {
        this.Activate = true;
        this.router.navigate(['/home/dashboard/']);
      }

    }
    
    if (data["required_2fa_2proceed"] === EnumAuthentication.change_password.toString()) {
      this.router.navigate(['/changepassword']);
    }
  }

  async AuthEnabled(data: ClientConfigResponse) {
    this.qr = true;
    this.Activate = true;
    var datatoken = await this.loginService.getTokenDecoded();

    if (datatoken["required_2fa_2proceed"] === EnumAuthentication.google.toString() && !this.verify) {
      this.flagtitle = true;

      this.title = "Google";

      this.Confirm = EnumAuthentication.google.toString();
    }
    if (datatoken["required_2fa_2proceed"] === EnumAuthentication.email.toString()) {
      this.flagtitle = false;
      this.notconfirm = true;

      this.title = "Email";
      this.Confirm = EnumAuthentication.email.toString();
      this.ConfirmEmail();
    }
    if (datatoken["required_2fa_2proceed"] === EnumAuthentication.sms.toString()) {
      this.notconfirm = true;
      this.flagtitle = false;

      this.title = "SMS";
      this.Confirm = EnumAuthentication.sms.toString();
      this.ConfirmSMS();
    }
    if (datatoken["required_2fa_2proceed"] === EnumAuthentication.change_password.toString()) {
      this.router.navigate(['/changepassword']);
    }
    if(!data.phone_validated || !data.email_validated || !data.id_verified || !data.kyc_completed){
      this.verify = true;
      this.AuthPending(data);
    }
  }

  AuthDeclined(data: ClientConfigResponse) {
    if (data["required_2fa_2proceed"] === EnumAuthentication.sms.toString()) {
      this.notconfirm = true;
      this.Activate = false;
      this.flagtitle = false;

      this.title = "SMS";
      this.Confirm = EnumAuthentication.sms.toString();
      this.ConfirmSMS();
    }
    if (data["required_2fa_2proceed"] === EnumAuthentication.none.toString()) {
      this.Activate = true;
      this.router.navigate(['/home/dashboard/']);
    }
  }

  async GetConfigClient() {
    this.CodeForm.reset();

    var data = await this.GetClientConfig();
    if(!data){
      return;
    }


    console.log("esto fas "+JSON.stringify(data))
    if (data["auth_choice"] === "auth_pending") {
      this.AuthPending(data);
    }
    if (data["auth_choice"] === "auth_enabled") {
      await this.AuthEnabled(data);
    }
    if (data["auth_choice"] === "auth_declined") {
      this.AuthDeclined(data);
    }

    if(localStorage.getItem('token') !== null && localStorage.getItem('remember') === "true" && data.state === undefined && data["auth_choice"] === "auth_enabled" && !this.verify){
      let msgstatus = await this.translate.get("general.succes").toPromise();
      this.toastr.success(msgstatus)
      this.router.navigate(['/home/dashboard/']);
      this.subscription.unsubscribe();
    }

  }



  validateEmail(Code: ValidateModel): void {
    this.validationservice.validate2faEmail(Code).subscribe(async data => {
      if (data.success) {
        let msgstatus = await this.translate.get("general.succes").toPromise();
        let msg = await this.translate.get("validation.Emailvalidation").toPromise();
        this.toastr.success(msg, msgstatus)
        if (this.Activate || this.verify) {
          this.router.navigate(['/home/dashboard/']);
          this.subscription.unsubscribe();

        } else {
          this.GetConfigClient();
        }
      } else {
        let msgstatus = await this.translate.get("general.error").toPromise();
        let msg = await this.translate.get("validation.ErrorCode").toPromise();
        this.toastr.error(msg, msgstatus, {
          closeButton: true,
          disableTimeOut: true,
          positionClass: 'toast-center-center',

        })
        this.CodeForm.reset();
      }
    }, error => {
      this.CodeForm.reset();
    });

  }

  validateSMS(Code: ValidateModel): void {
    console.log("Validando SMS", Code)
    this.validationservice.validate2faSMS(Code).subscribe(async data => {
      if (data.success) {
        let msgstatus = await this.translate.get("general.succes").toPromise();
        let msg = await this.translate.get("validation.SMSvalidation").toPromise();
        this.toastr.success(msg, msgstatus)

        if (this.Activate || this.verify) {
          this.router.navigate(['/home/dashboard/']);
          this.subscription.unsubscribe();

        } else {
          this.GetConfigClient();
        }
      } else {
        let msgstatus = await this.translate.get("general.error").toPromise();
        let msg = await this.translate.get("validation.ErrorCode").toPromise();
        this.toastr.error(msg, msgstatus, {
          closeButton: true,
          disableTimeOut: true,
          positionClass: 'toast-center-center',

        })
        this.CodeForm.reset();
      }
    }, error => {
      this.CodeForm.reset();
    });

  }


  validateGoogle(Code: ValidateModel): void {
    this.validationservice.validate2faGoogle(Code).subscribe(async data => {
      if (data.success) {
        let msgstatus = await this.translate.get("general.succes").toPromise();
        let msg = await this.translate.get("validation.Googlevalidation").toPromise();
        this.toastr.success(msg, msgstatus)
        if (this.Activate) {
          this.router.navigate(['/home/dashboard/']);
          this.subscription.unsubscribe();

        } else {
          this.GetConfigClient();
        }
      } else {
        let msgstatus = await this.translate.get("general.error").toPromise();
        let msg = await this.translate.get("validation.ErrorCode").toPromise();
        this.toastr.error(msg, msgstatus, {
          closeButton: true,
          disableTimeOut: true,
          positionClass: 'toast-center-center',

        })
        this.CodeForm.reset();
      }
    }, error => {
      this.CodeForm.reset();
    });
  }

  ConfirmEmail(): void {
    this.validationservice.ConfirmEmail().subscribe(async data => {
      let msgstatus = await this.translate.get("Errores.validateEmail").toPromise();
      this.toastr.warning(data.payload, msgstatus)
    }, error => {

    })

  }

  ConfirmSMS(): void {
    this.validationservice.ConfirmCelular().subscribe(async data => {
      let msgstatus = await this.translate.get("Errores.validateSMS").toPromise();

      this.toastr.warning(data.payload, msgstatus)
    }, error => {

    })
  }

  ConfirmID(): void {
    
    this.ConfirmValidateID();
  }

  async ConfirmQR(data: any): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    var date = {
      title: await this.translate.get('dialog.generate').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.sureGenerateCode').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), date);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['qr/' + data]);
      }

    }));
  }



  GenerateQR(): void {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "17rem";

    let dialogRef = this.dialog.open(ConfirmationGenerateQrComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumAuthentication.sms.toString()) {
        this.ConfirmQR(EnumAuthentication.sms.toString());
      }
      if (result == EnumAuthentication.email.toString()) {
        this.ConfirmQR(EnumAuthentication.email.toString())
      }

    }));
  }
  //************************************************************************************
  //Logout
  //************************************************************************************

  async Logout() {
    await this.LogoutService()
    this.loginService.removelocalStorage();
    this.router.navigate(['/']);
  }

  //************************************************************************************
  //Confirm Validate ID
  //************************************************************************************


  async ConfirmValidateID(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";

    var data = {
      title: await this.translate.get("Errores.validateID").toPromise(),
      icon: 'quest',
      conten: await this.translate.get("Errores.wantValidateID").toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/vouched']);
      }else if(result == EnumConfirmation.no.toString()){
        this.router.navigate(['/home']);
      }

    }));
  }
  //*******************************************************************************************
  //Service
  //*******************************************************************************************

  GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.loginService.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }

  LogoutService(): Promise<any>{
    return new Promise<void>((resolve, reject) => {
      this.subscription.add(this.loginService.logout().subscribe(response=>resolve(response),err=>resolve(null)))
    })

  }

}
