import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { ConfirmationComponent } from '../confirmation/confirmation.component';
import {confirm} from 'src/models/Confirm/confirm.model';
import { TranslateService } from '@ngx-translate/core';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ConfirmValidateGoogleQRComponent } from '../confirm-validate-google-qr/confirm-validate-google-qr.component';
import { ClipboardService } from 'ngx-clipboard';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-qr-view',
  templateUrl: './qr-view.component.html',
  styleUrls: ['./qr-view.component.scss']
})
export class QrViewComponent implements OnInit, OnDestroy {
  title: string = "";
  QR: string = "";
  keyQR:string = "";
  show: boolean = false;
  public subscription: Subscription = new Subscription();

  constructor(private translate:TranslateService, private _dialogRef:MatDialogRef<QrViewComponent>,private manage_data: ManageDataService<any>,
    private dialog: MatDialog, private toastr:ToastrService, private copy:ClipboardService) { }
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.GetDetails();
  }
  GetDetails():void{

    this.manage_data.getSubject<any>(EnumEventsPopUp.QR.toString()).subscribe(data=>{
        this.show = true;
        this.QR = data.qrcode;
        this.keyQR = data.key;
    })

  }

  async CopyKey(){
    this.copy.copyFromContent(this.keyQR);
    let msg = await this.translate.get('general.copied').toPromise();
    this.toastr.info(msg);
  }

  ConfirmValidateCode(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "25%";
    dialogConfig.width = "width: 57%";
    
    let dialogRef = this.dialog.open(ConfirmValidateGoogleQRComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.success.toString()) {
        this._dialogRef.close(EnumConfirmation.si.toString());
      }
    }));
  }
}
