import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { BarcodeFormat } from '@zxing/library';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ManageDataService } from 'src/services/manage-data.service';

@Component({
  selector: 'app-qr-reader',
  templateUrl: './qr-reader.component.html',
  styleUrls: ['./qr-reader.component.scss']
})
export class QrReaderComponent implements OnInit {
  
  allowedFormats = [ BarcodeFormat.QR_CODE ];
  scanEnable:boolean = true;
  constructor(private manage_data: ManageDataService<any>, private dialogRef:MatDialogRef<QrReaderComponent>) { }

  ngOnInit(): void {
  }

  ScanSuccess(data:any){
    //console.log("Success")
    this.scanEnable = false;
    this.manage_data.emitEvent(EnumEventsPopUp.QRReaderWithdraw.toString(),data);
    this.dialogRef.close();
  }

  ScanError(data:any){
    //console.log("Error", data)
    this.scanEnable = false;
   // console.log("Informacion del QR",data)
  }

  ScanFailure(data:any){
    //console.log("Failure")
    //this.scanEnable = false;
   // console.log("Informacion del QR",data)
  }

  CameraFound(cameras:any){
    //console.log("Cameras", cameras)

  }

  Close(){
    this.dialogRef.close();
  }

}
