import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { AppSettingsService } from 'src/services/AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class ClientService {
  ApiBase!:string ;
  ApiGetClientConfig!:string ;



  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };

  constructor(private http:HttpClient,private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiGetClientConfig = this.settingsService.getapidatos().APIS.ApiGetClientConfig;
  }


  GetClientConfig(): Observable<ClientConfigResponse>{
    return this.http.get(`${this.ApiBase}${this.ApiGetClientConfig}`,this.options).pipe(
      map((res: ClientConfigResponse) => {
        return res;
      })
    );
  }
}
