import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import {confirm} from 'src/models/Confirm/confirm.model';
import { ConfirmationComponent } from '../confirmation/confirmation.component';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';

@Component({
  selector: 'app-funds-options',
  templateUrl: './funds-options.component.html',
  styleUrls: ['./funds-options.component.scss']
})
export class FundsOptionsComponent implements OnInit, OnDestroy {

  data:any;
  title:any = '';

  public subscription: Subscription = new Subscription();

  constructor(private manage_data: ManageDataService<any>, private dialogRef:MatDialogRef<FundsOptionsComponent>,
    private translate:TranslateService, private dialog:MatDialog) { }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.FundsOption.toString()).subscribe(async data=>{
      this.data = data;
      if(this.data.direction == 'in'){
        this.title = await this.translate.get('funds.fundsIn').toPromise();
      }else if(this.data.direction == 'out'){
        this.title = await this.translate.get('funds.fundsOut').toPromise();
      }
    }));
  }

  async ConfirmDelete():Promise<void>{

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";
    
    var data ={
      title: await this.translate.get("dialog.deletePaymentMethod").toPromise(),
      icon: 'delet',
      conten: await this.translate.get("dialog.sureDeletePayment").toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(),data);
    let dialog = this.dialog.open(ConfirmationComponent,dialogConfig);

    this.subscription.add(dialog.afterClosed().subscribe(result => {
      if(result == EnumConfirmation.si.toString()){
        this.Delete();
      }
  
    }));
  }

  Delete():void{
    this.dialogRef.close(EnumConfirmation.delete.toString());
  }

  Default():void{
    this.dialogRef.close(EnumConfirmation.default.toString());
  }

  Close():void{
    this.dialogRef.close();
  }
}
