import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import {confirm} from 'src/models/Confirm/confirm.model';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.scss']
})
export class ConfirmationComponent{
 conten: confirm;
  constructor(private dialogRef:MatDialogRef<ConfirmationComponent>,private manage_data: ManageDataService<any>) {
    this.manage_data.getSubject<confirm>(EnumEventsPopUp.ConfirmAction.toString()).subscribe(data=>{
      this.conten=data;
      
    })
   }

  confirmationYes():void{
    this.dialogRef.close(EnumConfirmation.si.toString());
  }

  confirmationNo():void{
    this.dialogRef.close(EnumConfirmation.no.toString());

  }
}
