import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { AppSettingsService } from 'src/services/AppSettingsInit/app-settings.service';
import { ClientService } from 'src/services/Client/client.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit, OnDestroy {

  data_client:any;
  ApiBase!:string;
  SessionID:string;
  Version:any;


  subscription:Subscription = new Subscription();

  constructor(private dialogRef:MatDialogRef<AboutComponent>, private client:ClientService, private settingsService: AppSettingsService) { }
 
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.GetClientConfig()
    .then((data:any)=>{
      this.data_client = data;
      this.SessionID = this.data_client.key.toString().substring(this.data_client.key.toString().indexOf('|')+1,this.data_client.key.toString().length)
      this.Version = window['version'];

    })
    .catch()

  }

  OKConfirm():void{
    this.dialogRef.close();

  }
  //*******************************************************
  //Services
  //*******************************************************

  GetClientConfig(): Promise<any>{

    return new Promise<ClientConfigResponse>((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((response:ClientConfigResponse)=>{
        resolve(response);
      },err=>{
        resolve(null);
      }))
    })
     
  }
}
