import { Injectable, OnDestroy } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { map, Observable, Subscription } from 'rxjs';
import { LoginService } from '../login/login.service';
@Injectable({
  providedIn: 'root'
})

export class DashboardGuard implements CanActivate{

  constructor(private router: Router, private toastr: ToastrService, private loginService: LoginService,
    private translate:TranslateService) { 
    
  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

       return new Promise((resolve, reject) => {
            this.loginService.GetClientConfig().subscribe(async (data) => {
              if (data['required_2fa_2proceed'] == 'NONE') {

                if(data['id_verified'] === false||data['kyc_completed'] === false){
                  let msg = await this.translate.get('Errores.pleaseValidateID').toPromise();
                  this.toastr.warning(msg)
                }
              return resolve(true);
            }else{
              let msg = await this.translate.get('Errores.pleaseValidateCode').toPromise();
              this.toastr.warning(msg)
              this.router.navigate(["/validation"])
              return resolve(false);
            }
            },async error=>{
              let msg = await this.translate.get('Errores.pleaseValidateCode').toPromise();
              this.toastr.warning(msg)
              this.router.navigate(["/validation"])
              return resolve(false);
            });
          }).then((data:boolean)=>{
            console.log("Error ++++++",data)
            return data;
          });

  }


}
