import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { GenerateQRModel, GenerateQRModelResponse } from 'src/models/GenerateQR/generateQR.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class GenerateQRService {
  ApiBase!:string ;
  ApiGenerateQRGoogle!:string ;
  ApiGenerateQRSMS!:string ;
  ApiGenerateQREmail!:string ;

  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
  });
  options = { headers: this.headers };

 
  constructor(private http:HttpClient,private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiGenerateQRGoogle = this.settingsService.getapidatos().APIS.ApiGenerateQRGoogle;
    this.ApiGenerateQRSMS = this.settingsService.getapidatos().APIS.ApiGenerateQRSMS;
    this.ApiGenerateQREmail = this.settingsService.getapidatos().APIS.ApiGenerateQREmail;
   }


  generateQR2faGoogle(): Observable<any>{
    return this.http.get(this.ApiBase + this.ApiGenerateQRGoogle,this.options);
  }
  generateQR2faSMS(data:GenerateQRModel): Observable<GenerateQRModelResponse>{
    return this.http.post<GenerateQRModelResponse>(this.ApiBase + this.ApiGenerateQRSMS, data,this.options).pipe(
      map((res: GenerateQRModelResponse) => {
        return res;
      })
    );
  } 
  generateQR2faEmail(data:GenerateQRModel): Observable<GenerateQRModelResponse>{
    return this.http.post<GenerateQRModelResponse>(this.ApiBase + this.ApiGenerateQREmail, data,this.options).pipe(
      map((res: GenerateQRModelResponse) => {
        return res;
      })
    );
  }



}
