import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { BalanceModel, BalanceModelResponse } from 'src/models/Portafolio/portfolioBalance.model';
import { BalanceForSymbolModelResponse, BalanceForSymbolModel } from 'src/models/Portafolio/portfolioBalanceForSymbol.model';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { PortafolioInfo } from 'src/models/Portafolio/portfolioInfo.model';
import { LiquidateAllAssetsModel } from 'src/models/Portafolio/portfolioLiquidate.model';
import { OrderDetailsModel, OrderDetailsModelResponse } from 'src/models/Portafolio/portfolioOrderDetails.model';
import { AllProducts } from 'src/models/Portafolio/portfolioProducts.model';
import { TransactionModelResponse, TransactionModel } from 'src/models/Portafolio/portfolioTransaction.model';
import { DepositModel, DepositResponseModel, WithdrawMaxModel, WithdrawModel, WithdrawModelResponse } from 'src/models/TestFounds/testFounds.model';
import { GetGasFeeEstimateModel, GetGasFeeEstimateModelResponse } from 'src/models/Wallet/wallet.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class PortafolioService {
  ApiBase:string
  ApiGetDefaultPortfolio:string
  ApiFoundsIn!:string;
  ApiFoundsOut!:string;
  ApiGetAllProducts!:string;
  ApiGetWalletForDeposit!:string;
  ApiGetPortfolioBalances!:string;
  ApiGetGasFeeEstimate!:string;
  ApiGetPortfolioBalancesForSymbol!:string;
  ApiWithdrawMax!:string;
  ApiGetDeepPortfolioInfo!:string;
  ApiLiquidateAll!:string;
  ApiGetOrderDetail!:string;
  ApiGetPortfolioTransactions!:string;

  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };
  
  constructor(private http:HttpClient, private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiGetDefaultPortfolio = this.settingsService.getapidatos().APIS.ApiGetDefaultPortfolio;
    this.ApiFoundsIn = this.settingsService.getapidatos().APIS.ApiFoundsIn;
    this.ApiFoundsOut = this.settingsService.getapidatos().APIS.ApiFoundsOut;
    this.ApiGetAllProducts = this.settingsService.getapidatos().APIS.ApiGetAllProducts;
    this.ApiGetWalletForDeposit = this.settingsService.getapidatos().APIS.ApiGetWalletForDeposit;
    this.ApiGetPortfolioBalances = this.settingsService.getapidatos().APIS.ApiGetPortfolioBalances;
    this.ApiGetGasFeeEstimate = this.settingsService.getapidatos().APIS.ApiGetGasFeeEstimate;
    this.ApiGetPortfolioBalancesForSymbol = this.settingsService.getapidatos().APIS.ApiGetPortfolioBalancesForSymbol;
    this.ApiWithdrawMax = this.settingsService.getapidatos().APIS.ApiWithdrawMax;
    this.ApiGetDeepPortfolioInfo = this.settingsService.getapidatos().APIS.ApiGetDeepPortfolioInfo;
    this.ApiLiquidateAll = this.settingsService.getapidatos().APIS.ApiLiquidateAll;
    this.ApiGetOrderDetail = this.settingsService.getapidatos().APIS.ApiGetOrderDetail;
    this.ApiGetPortfolioTransactions = this.settingsService.getapidatos().APIS.ApiGetPortfolioTransactions;
  }
  

  GetDefault(): Observable<PortafolioDefault>{
    return this.http.get<PortafolioDefault>(`${this.ApiBase}${this.ApiGetDefaultPortfolio}`,this.options).pipe(
      map((res: PortafolioDefault) => {
        return res;
      })
    );
  }
  
  GetAllProducts(): Observable<AllProducts[]>{
    return this.http.get<AllProducts[]>(`${this.ApiBase}${this.ApiGetAllProducts}`,this.options).pipe(
      map((res: AllProducts[]) => {
        return res;
      })
    );
  }

  GetDeepPortfolioInfo(): Observable<PortafolioInfo[]>{
    return this.http.post<PortafolioInfo[]>(`${this.ApiBase}${this.ApiGetDeepPortfolioInfo}`,null,this.options).pipe(
      map((res: PortafolioInfo[]) => {
        return res;
      })
    );
  }


  GetPortafolioBalances(data:BalanceModel): Observable<BalanceModelResponse[]>{
    return this.http.post<BalanceModelResponse[]>(this.ApiBase+this.ApiGetPortfolioBalances,data,this.options).pipe(
      map((res: BalanceModelResponse[]) => {
        return res;
      })
    );
  }
  
  GetPortafolioBalancesForSymbol(data:BalanceForSymbolModel): Observable<BalanceForSymbolModelResponse>{
    return this.http.post<BalanceForSymbolModelResponse>(this.ApiBase+this.ApiGetPortfolioBalancesForSymbol,data,this.options).pipe(
      map((res: BalanceForSymbolModelResponse) => {
        return res;
      })
    );
  }

  GetPortafolioTransaction(data:TransactionModel): Observable<TransactionModelResponse[]>{
    
    return this.http.post<TransactionModelResponse[]>(this.ApiBase+this.ApiGetPortfolioTransactions,data,this.options).pipe(
      map((res: TransactionModelResponse[]) => {
        return res;
      })
    );
  }

  GetGasFeeEstimate(data:GetGasFeeEstimateModel): Observable<GetGasFeeEstimateModelResponse>{
    return this.http.post<GetGasFeeEstimateModelResponse>(this.ApiBase+this.ApiGetGasFeeEstimate,data,this.options).pipe(
      map((res: GetGasFeeEstimateModelResponse) => {
        return res;
      })
    );
  }
  GetOrderDetail(data:OrderDetailsModel): Observable<OrderDetailsModelResponse>{
    return this.http.post<OrderDetailsModelResponse>(this.ApiBase+this.ApiGetOrderDetail,data,this.options).pipe(
      map((res: OrderDetailsModelResponse) => {
        return res;
      })
    );
  }

  Deposit(data:DepositModel): Observable<DepositResponseModel>{
    return this.http.post<DepositResponseModel>(this.ApiBase+this.ApiFoundsIn,data,this.options).pipe(
      map((res: DepositResponseModel) => {
        return res;
      })
    );
  } 

  Withdraw(data:WithdrawModel): Observable<WithdrawModelResponse>{
    return this.http.post<WithdrawModelResponse>(this.ApiBase+this.ApiFoundsOut,data,this.options).pipe(
      map((res: WithdrawModelResponse) => {
        return res;
      })
    );
  }

  WithdrawMax(data:WithdrawMaxModel): Observable<WithdrawModelResponse>{
    return this.http.post<WithdrawModelResponse>(this.ApiBase+this.ApiWithdrawMax,data,this.options).pipe(
      map((res: WithdrawModelResponse) => {
        return res;
      })
    );
  }

  LiquiateAllAssetsPortafolio(data:LiquidateAllAssetsModel): Observable<any>{
    return this.http.post(this.ApiBase+this.ApiLiquidateAll,data,this.options);
  }

  GetPortafolioTransactionForSymbol(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  GetAllUsersPortafolios(): Observable<any>{
    return this.http.get("http://localhost:4200/login",this.options);
  }

  BuyMarketByAmount(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  BuyToTrackingPortafolio(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  SellMarketByAmount(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  SellMarketByQty(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  SellFromTrackingPortafolio(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  LiquiateAllTrackingPortafolio(data:any): Observable<any>{
    return this.http.post("http://localhost:4200/login",data,this.options);
  }

  CancelOrder(data:any): Observable<any>{
    return this.http.put("http://localhost:4200/login",data,this.options);
  }


  
}
