import { AfterViewInit, Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { ValidationService } from 'src/services/validation/validation.service';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subject, Subscription } from 'rxjs';


declare function Vouched(any): any

@Component({
  selector: 'app-vouched',
  templateUrl: './vouched.component.html',
  styleUrls: ['./vouched.component.scss']
})
export class VouchedComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('codeInput') codeInputElement!: ElementRef<HTMLInputElement>;

  vouchedForm: FormGroup;
  ErroresSpan: any = listadeErrores;
  validate: boolean = true;
  language:string = "";
  vouched:any;
  
  public vouched_notify = new Subject<any>();
  public subscription:Subscription = new Subscription();

  constructor(private fb: FormBuilder,private validateservice: ValidationService, private toastr: ToastrService, private router: Router,private translate:TranslateService) {
    this.vouchedForm = this.fb.group({
      name: ['', Validators.required],
      lastname: ['', Validators.required],
    })
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngAfterViewInit(): void {
    this.codeInputElement.nativeElement.focus();
  }
  
  ngOnInit(): void {
    let language_navigator = window.navigator.language;
    this.language = language_navigator.split("-")[0];
  }

  async ManageResponse(){
    let data = await this.GetData()

    const id:any = {
      jobid : data.id,
    }     
    this.subscription.add(this.validateservice.ConfirmID(id).subscribe(async response => {
      console.log("Response Data",response);
      console.log("Response result",response.success);

      if (response.success) {
        let msg = await this.translate.get("validation.Idvalidation").toPromise();
        let msgstatus  = await this.translate.get("general.succes").toPromise();
        this.toastr.success(msg,msgstatus)
        this.router.navigate(['/home']);   

      } else {
        this.vouched.unmount();
        this.ShowError(response.payload, "Error");            
        this.validate = true;   
      }
      

    }, error => {         
      this.validate = true;   
    }));

    }

  Register(): void {
    
    this.ManageResponse();
    this.vouchedForm.value.name
    this.vouchedForm.value.lastname
    var vouchedRequest: Object = {}
    vouchedRequest['firstName'] = this.vouchedForm.value.name
    vouchedRequest['lastName'] = this.vouchedForm.value.lastname

    const updateUser:any = {
      fname :this.vouchedForm.value.name,
      lname:this.vouchedForm.value.lastname
    }      

    this.validateservice.UpdateName(updateUser).toPromise();
    this.validate = false;
    this.vouched = Vouched({
      // Optional verification properties.
      verification: {
        firstName: this.vouchedForm.value.name,
        lastName: this.vouchedForm.value.lastname,
      },
      liveness: 'straight',
      appId: '.Y1.~@59utQV#xf4LRsa~PEJaBz1wP',
      // mobile handoff
      crossDevice: true,
      crossDeviceQRCode: true,
      locale: this.language,

      onInit: (job) => {
        // console.log("Scanning Init", job);
      },
      // called when the verification is completed.
      onDone: (job) => {
        // token used to query jobs
        // console.log("Scanning complete", job);
        this.vouched_notify.next(job);

      },
      // theme
      theme: {
        name: 'verbose',
        bgColor: '#0000',
      },
      logo: { src: 'https://www.vouched.id/wp-content/uploads/2020/11/vouched_logo_hi_res.png', style: { 'max-width': 150, 'margin-bottom': 30 } }
    });
    this.vouched.mount("#vouched-element");

  };

  GetData():Promise<any>{
    return new Promise<any>((resolve, reject) => {
      this.subscription.add(this.GetVouchedNotify().subscribe(response =>{
        resolve(response);
      }))
    })
  }

  GetVouchedNotify():Observable<any>{
    return this.vouched_notify.asObservable();
  }

  //*******************************************************************************************
  //PopUp Error
  //*******************************************************************************************

  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }


}






