import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { EnumAuthentication } from 'src/models/Enum/EnumAuthentication.enum';

@Component({
  selector: 'app-confirmation-generate-qr',
  templateUrl: './confirmation-generate-qr.component.html',
  styleUrls: ['./confirmation-generate-qr.component.scss']
})
export class ConfirmationGenerateQrComponent {

  constructor(private dialogRef:MatDialogRef<ConfirmationGenerateQrComponent>) { }

  SMSQR(): void {
    this.dialogRef.close(EnumAuthentication.sms.toString());
  }

  EmailQR(): void {
    this.dialogRef.close(EnumAuthentication.email.toString());
  }

  close(): void {
    this.dialogRef.close();
  }
}
