import { APP_INITIALIZER, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoginComponent } from './UserPages/login/login.component';
import { RegisterComponent } from './UserPages/register/register.component';
import { AddTokenInterceptor } from 'src/services/interceptors/add-token.interceptor';
import { BaseLayoutComponent } from './layout/base-layout/base-layout.component';
import { FooterComponent } from './layout/footer/footer.component';
import { HeaderComponent } from './layout/header/header.component';
import { SidebarComponent } from './layout/sidebar/sidebar.component';
import { AppSettingsService } from 'src/services/AppSettingsInit/app-settings.service';
import { ValidationComponent } from './UserPages/validation/validation.component';
import { GenerateQRComponent } from './Options/generate-qr/generate-qr.component';
import { NgbCollapseModule, NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { ToastrModule } from 'ngx-toastr';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { VouchedComponent } from './UserPages/vouched/vouched.component';
import { NgxPermissionsModule } from 'ngx-permissions';
import { ForgotPasswordComponent } from './UserPages/forgot-password/forgot-password.component';
import { ChangePasswordComponent } from './UserPages/change-password/change-password.component';
import { MatDialogModule } from '@angular/material/dialog';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { ConfirmationGenerateQrComponent } from './PopUp/confirmation-generate-qr/confirmation-generate-qr.component';
import { QrViewComponent } from './PopUp/qr-view/qr-view.component';
import { HttpErrorInterceptor } from 'src/services/interceptors/http-error.interceptor';
import { ConfirmationComponent } from './PopUp/confirmation/confirmation.component';
import { CommonModule } from '@angular/common';
//scanner
import { QrReaderComponent } from './PopUp/qr-reader/qr-reader.component';
import { ZXingScannerModule } from '@zxing/ngx-scanner';
import { FundsOptionsComponent } from './PopUp/funds-options/funds-options.component';
import { DetailsTransactionsPortfolioComponent } from './PopUp/details-transactions-portfolio/details-transactions-portfolio.component';
import { BuySellComponent } from './BuyAndSell/buy-sell/buy-sell.component';
import { NgxSpinnerModule } from "ngx-spinner";
import { MaterialModule } from 'src/Modules/material-module';
import { ConfirmValidateGoogleQRComponent } from './PopUp/confirm-validate-google-qr/confirm-validate-google-qr.component';
import { ChangeEmailComponent } from './UserPages/change-email/change-email.component';
import { ChangePhoneComponent } from './UserPages/change-phone/change-phone.component';
import { ClipboardModule } from 'ngx-clipboard';
import { ScrollToTopComponent } from './shared/scroll-to-top/scroll-to-top.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';

export function cargarTraductor(http: HttpClient) {
  return new TranslateHttpLoader(http, '../assets/i18n/', '.json');
}


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    BaseLayoutComponent,
    FooterComponent,
    HeaderComponent,
    SidebarComponent,
    ValidationComponent,
    GenerateQRComponent,
    VouchedComponent,
    ForgotPasswordComponent,
    ChangePasswordComponent,
    ConfirmationGenerateQrComponent,
    QrViewComponent,
    ConfirmationComponent,
	  QrReaderComponent,
    FundsOptionsComponent,
    DetailsTransactionsPortfolioComponent,
    BuySellComponent,
    ConfirmValidateGoogleQRComponent,
    ChangeEmailComponent,
    ChangePhoneComponent,
    ScrollToTopComponent
  ],
  imports: [
    CommonModule,
    NgbModule,
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    FontAwesomeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgbCollapseModule,
    NgxSpinnerModule,
    ClipboardModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (cargarTraductor),
        deps: [HttpClient]
      }

    }),
	  ZXingScannerModule,
    ToastrModule.forRoot(),
    NgxPermissionsModule.forRoot(),
    MatDialogModule,
	  MaterialModule,
   ServiceWorkerModule.register('ngsw-worker.js', {
     enabled: environment.production,
     // Register the ServiceWorker as soon as the application is stable
     // or after 30 seconds (whichever comes first).
     registrationStrategy: 'registerWhenStable:30000'
   })


  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: HttpErrorInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: AddTokenInterceptor, multi: true },
    {
      provide: APP_INITIALIZER,
      multi: true,
      deps: [AppSettingsService],
      useFactory: (appConfigService: AppSettingsService) => {
        return () => {
          return appConfigService.loadSettings();
        };
      }
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
