import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ConfirmationComponent } from 'src/app/PopUp/confirmation/confirmation.component';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ChangeEmailModel } from 'src/models/login/login.model';
import { LoginService } from 'src/services/login/login.service';
import { confirm } from 'src/models/Confirm/confirm.model';
import { ManageDataService } from 'src/services/manage-data.service';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { ClientService } from 'src/services/Client/client.service';


@Component({
  selector: 'app-change-email',
  templateUrl: './change-email.component.html',
  styleUrls: ['./change-email.component.scss']
})
export class ChangeEmailComponent implements OnInit, OnDestroy {
  
  loginForm: FormGroup;
  public subscription: Subscription = new Subscription();
  ErroresSpan: any = listadeErrores;
  validating:boolean = false;
  current_email:string;


  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router, private toastr: ToastrService,
    private translate:TranslateService, private manage_data:ManageDataService<any>, private dialog: MatDialog,
    private client:ClientService) {
    
    this.loginForm = this.fb.group({
      new_email :['',Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
      ])]
    })
   }

   ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  async ngOnInit(){
    let data = await this.GetClientConfig()
    this.current_email = data.email;
  }

  async UpdateEmail(){
    this.validating = true;

    if(this.current_email.trim() == this.loginForm.value.new_email){
      this.validating = false;
      let title = await this.translate.get('general.error').toPromise();
      let msg = await this.translate.get('ChangeEmail.errorEmailSame').toPromise();
      this.ShowError(msg, title);
      return; 
    }
    

    const usuario: ChangeEmailModel = {
      new_email: this.loginForm.value.new_email
    }

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";

    var content_text = await this.translate.get("dialog.sureChangeEmail").toPromise();

    var data = {
      title: await this.translate.get("ChangeEmail.change").toPromise(),
      icon: 'quest',
      conten: content_text + '\n ' + usuario.new_email
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.subscription.add(this.loginService.ChangeEmail(usuario).subscribe(async data => {
          this.router.navigate(['validation/']);  
          let msg = await this.translate.get("ChangeEmail.changeEmail").toPromise();
          let msgstatus  = await this.translate.get("general.succes").toPromise();
          this.toastr.success(msg,msgstatus)
          // this.toastr.success("Correo Cambiado!!")
          this.validating = false;
          
        }, error => {
          this.validating = false;
          this.loginForm.reset();
        }));
      }else if(result == EnumConfirmation.no.toString()){
        this.router.navigate(['/home']);  
      }

    }));
  }

  //===========================================================================
  // Show Error
  //===========================================================================

  ShowError(msg: string, title: string = "", disableTimeOut: boolean = true) {
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,
      positionClass: 'toast-center-center',
    });
  }

  //***************************************************************************************
  //Services
  //***************************************************************************************

  GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise<ClientConfigResponse>((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }

}
