import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { PasswordStrengthValidator } from 'src/services/FormValidation/passwordStrength.validator';
import { LoginService } from 'src/services/login/login.service';
import listadeErrores from 'src/assets/Errores/Errores.json';
import { TranslateService } from '@ngx-translate/core';
import { ChangePassModel } from 'src/models/login/login.model';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit ,OnDestroy{
  loginForm: FormGroup;
  public subscription: Subscription = new Subscription();
  ErroresSpan: any = listadeErrores;
  
  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router, private toastr: ToastrService,private translate:TranslateService) {
    this.loginForm = this.fb.group({
      password: ['', Validators.compose([
        Validators.required,
        Validators.minLength(8),        

      ])],
      passwordConfirmation: ['', Validators.compose([
        Validators.required,
        Validators.minLength(8),
        PasswordStrengthValidator

      ])]
    })
  }
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
  ngOnInit(): void {
  }
  UpdatePassword(): void {

    const usuario: ChangePassModel = {
      current_password: this.loginForm.value.password,
      new_password: this.loginForm.value.passwordConfirmation
    }

    this.subscription.add(this.loginService.ChangePassword(usuario).subscribe(async data => {
      let msg = await this.translate.get("ChangePassword.ChangePassword").toPromise();
      let msgstatus  = await this.translate.get("general.succes").toPromise();
      this.toastr.success(msg,msgstatus)
      this.toastr.success("Contrasea Cambiada!!")
      localStorage.removeItem('token')
      this.router.navigate(['/']);  

    }, error => {
      this.loginForm.reset();
    }));


  }
}
