import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { QrViewComponent } from 'src/app/PopUp/qr-view/qr-view.component';
import { EnumAuthentication, EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { GenerateQRModel, GenerateQRModelResponse } from 'src/models/GenerateQR/generateQR.model';
import { GenerateQRService } from 'src/services/GenerateQR/generate-qr.service';
import { ManageDataService } from 'src/services/manage-data.service';
import { ValidationService } from 'src/services/validation/validation.service';

@Component({
  selector: 'app-generate-qr',
  templateUrl: './generate-qr.component.html',
  styleUrls: ['./generate-qr.component.scss']
})
export class GenerateQRComponent implements OnInit,OnDestroy {
  CodeForm: FormGroup;
  Confirm: string = "";
  title: string = "";
  QR: string = "";
  show: boolean = false;
  showinput:boolean=false;

  public subscription: Subscription = new Subscription();

  constructor(private translate:TranslateService,private manage_data: ManageDataService<any>,private fb: FormBuilder, 
    private validationservice: ValidationService,private dialog: MatDialog,private router: Router, private ruteActive: ActivatedRoute, 
    private generateqrservice: GenerateQRService, private toastr: ToastrService) {

      this.CodeForm = this.fb.group({
        code: ['', Validators.compose([
          Validators.required,
          Validators.minLength(6),
        ])],
      })

    this.ChangedInput();

   }




   ChangedInput(){
    this.subscription.add(this.CodeForm.valueChanges.subscribe(value => {
      
      if (this.Confirm == EnumAuthentication.google.toString()) {
        if (value.code.length == 6) {
          this.EnterCode();
        }
      }
      if (this.Confirm == EnumAuthentication.sms.toString() || this.Confirm == EnumAuthentication.email.toString()) {
        if (value.code.length == 8) {
          this.EnterCode();
        }
      }

    }))
  }

   EnterCode(): void {
    const Code: any = {
      code: this.CodeForm.value.code,
    }
   
    if (this.Confirm == EnumAuthentication.sms.toString()) {
      this.GenerateQRSMS(Code);
      this.CodeForm.reset();
    }
    if (this.Confirm == EnumAuthentication.email.toString()) {
      this.GenerateQREmail(Code);
      this.CodeForm.reset();
    }
  }

  async ConfirmEmail(): Promise<void> {
    var msg = await this.translate.get('Errores.validateEmail').toPromise();
    this.validationservice.ConfirmEmail().subscribe(data => {
      this.toastr.warning(data.payload, msg)
    }, error => {
      this.toastr.error(error.error.payload, msg)
    })

  }

  async ConfirmSMS(): Promise<void> {
    var msg = await this.translate.get('Errores.validateSMS').toPromise();
    this.validationservice.ConfirmCelular().subscribe(data => {
      this.toastr.warning(data.payload, msg)
    }, error => {
      this.toastr.error(error.error.payload, msg)
    })
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.Confirm = this.ruteActive.snapshot.params['generate2fa'];
    if (this.Confirm == EnumAuthentication.google.toString()) {     
      this.showinput = false;
    }

    if (this.Confirm == EnumAuthentication.sms.toString()) {      
      this.showinput = true;
      this.title = EnumAuthentication.sms.toString();
      this.Confirm =  EnumAuthentication.sms.toString();
      this.ConfirmSMS();
      
    }

    if (this.Confirm == EnumAuthentication.email.toString()) {
      this.showinput = true;
      this.title = EnumAuthentication.email.toString();
      this.Confirm =  EnumAuthentication.email.toString();
      this.ConfirmEmail();

    }
  }

  Back(): void {

    this.router.navigate([''])

  }

  async GenerateQRGoogle():Promise<void>{
    var msg = await this.translate.get('Errores.errorQr').toPromise();
    this.generateqrservice.generateQR2faGoogle().subscribe((data:GenerateQRModelResponse) => {
      this.SendatosQR(data);
    }, error => {
      console.log(msg + JSON.stringify(error))
      this.toastr.error(error.error.payload, msg)

    });
  }

  async GenerateQRSMS(data:GenerateQRModel):Promise<void>{
    var msg = await this.translate.get('Errores.errorQr').toPromise();
    this.generateqrservice.generateQR2faSMS(data).subscribe((data:GenerateQRModelResponse) => {
      this.SendatosQR(data);
    }, error => {
      console.log(msg + JSON.stringify(error))
      this.toastr.error(error.error.payload, msg)

    });
  }

  async GenerateQREmail(data:GenerateQRModel):Promise<void>{
    var msg = await this.translate.get('Errores.errorQr').toPromise();
    this.generateqrservice.generateQR2faEmail(data).subscribe((data:GenerateQRModelResponse) => {
      this.SendatosQR(data);
    }, error => {
      console.log(msg + JSON.stringify(error))
      this.toastr.error(error.error.payload, msg)

    });
  }

  SendatosQR(data:GenerateQRModelResponse):void{
    this.manage_data.emitEvent<any>(EnumEventsPopUp.QR.toString(),data)

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    dialogConfig.width = "24rem";
    let dialogRef = this.dialog.open(QrViewComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/'])
      }
      if (result == EnumConfirmation.no.toString()) {
        this.router.navigate(['qr/'+this.Confirm])
     }
    }));
  }



}




